import React, { useState } from 'react';
import { Phone, Mail, MapPin, Clock, Send, CheckCircle, Building, Globe, Users } from 'lucide-react';

const ContactUs = () => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    phone: '',
    subject: '',
    message: ''
  });
  const [isSubmitted, setIsSubmitted] = useState(false);

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    // Here you would typically send the form data to your backend
    console.log('Form submitted:', formData);
    setIsSubmitted(true);
    
    // Reset form after submission
    setTimeout(() => {
      setIsSubmitted(false);
      setFormData({
        name: '',
        email: '',
        phone: '',
        subject: '',
        message: ''
      });
    }, 3000);
  };

  const contactInfo = [
    {
      icon: Phone,
      title: 'Phone',
      details: ['0711 02084', '+254 700 000 001'],
      action: 'tel:071102084'
    },
    {
      icon: Mail,
      title: 'Email',
      details: ['info@inukaproperties.co.ke', 'sales@inukaproperties.co.ke'],
      action: 'mailto:info@inukaproperties.co.ke'
    },
         {
       icon: MapPin,
       title: 'Head Office',
       details: ['Nyali, Mombasa', 'Along Links Road, Opposite Kigothos Hotel and Apartment'],
       action: '#'
     },
         {
       icon: Clock,
       title: 'Business Hours',
       details: ['Mon - Fri: 8:00 AM - 5:00 PM', 'Sat: 9:00 AM - 1:00 PM'],
       action: null
     }
  ];

     const locations = [
     {
       name: 'Head Office',
       address: 'Nyali, Mombasa - Along Links Road, Opposite Kigothos Hotel and Apartment',
       phone: '0711 02084',
       email: 'info@inukaproperties.co.ke',
       icon: Building
     }
   ];

  const services = [
    {
      icon: Building,
      title: 'Property Consultation',
      description: 'Get expert advice on property investment and development opportunities.'
    },
    {
      icon: Globe,
      title: 'Market Analysis',
      description: 'Comprehensive market research and property valuation services.'
    },
    {
      icon: Users,
      title: 'Customer Support',
      description: 'Dedicated support team available to assist with all your queries.'
    }
  ];

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Hero Section */}
      <section className="bg-gradient-to-r from-primary-700 to-primary-800 text-white py-20">
        <div className="container-custom text-center">
          <h1 className="text-5xl font-bold mb-6">Contact Us</h1>
          <p className="text-xl text-gray-200 max-w-3xl mx-auto">
            Get in touch with our team for personalized assistance with your real estate needs. 
            We're here to help you find the perfect property investment.
          </p>
        </div>
      </section>

      {/* Contact Information */}
      <section className="py-16">
        <div className="container-custom">
          <div className="grid md:grid-cols-2 lg:grid-cols-4 gap-8">
            {contactInfo.map((info, index) => (
              <div key={index} className="text-center">
                <div className="w-16 h-16 bg-primary-100 rounded-full flex items-center justify-center mx-auto mb-4">
                  <info.icon size={32} className="text-primary-500" />
                </div>
                <div className="text-xl font-bold text-gray-900 mb-2">{info.title}</div>
                {info.details.map((detail, detailIndex) => (
                  <div key={detailIndex} className="text-gray-600 mb-1">
                    {info.action ? (
                      <a 
                        href={info.action}
                        className="hover:text-primary-500 transition-colors"
                      >
                        {detail}
                      </a>
                    ) : (
                      <span>{detail}</span>
                    )}
                  </div>
                ))}
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* Main Contact Section */}
      <section className="py-16">
        <div className="container-custom">
          <div className="grid lg:grid-cols-2 gap-12">
            {/* Contact Form */}
            <div className="bg-white rounded-2xl shadow-xl p-8">
              <h2 className="text-3xl font-bold text-gray-900 mb-6">Send us a Message</h2>
              
              {isSubmitted ? (
                <div className="text-center py-12">
                  <CheckCircle size={64} className="text-green-500 mx-auto mb-4" />
                  <h3 className="text-2xl font-bold text-gray-900 mb-2">Message Sent!</h3>
                  <p className="text-gray-600">
                    Thank you for contacting us. We'll get back to you within 24 hours.
                  </p>
                </div>
              ) : (
                <form onSubmit={handleSubmit} className="space-y-6">
                  <div className="grid md:grid-cols-2 gap-6">
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-2">
                        Full Name *
                      </label>
                      <input
                        type="text"
                        name="name"
                        value={formData.name}
                        onChange={handleInputChange}
                        className="input-field"
                        placeholder="Enter your full name"
                        required
                      />
                    </div>
                    
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-2">
                        Email Address *
                      </label>
                      <input
                        type="email"
                        name="email"
                        value={formData.email}
                        onChange={handleInputChange}
                        className="input-field"
                        placeholder="Enter your email"
                        required
                      />
                    </div>
                  </div>
                  
                  <div className="grid md:grid-cols-2 gap-6">
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-2">
                        Phone Number
                      </label>
                      <input
                        type="tel"
                        name="phone"
                        value={formData.phone}
                        onChange={handleInputChange}
                        className="input-field"
                        placeholder="Enter your phone number"
                      />
                    </div>
                    
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-2">
                        Subject *
                      </label>
                      <input
                        type="text"
                        name="subject"
                        value={formData.subject}
                        onChange={handleInputChange}
                        className="input-field"
                        placeholder="What is this about?"
                        required
                      />
                    </div>
                  </div>
                  
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">
                      Message *
                    </label>
                    <textarea
                      name="message"
                      value={formData.message}
                      onChange={handleInputChange}
                      rows={6}
                      className="input-field"
                      placeholder="Tell us more about your inquiry..."
                      required
                    />
                  </div>
                  
                  <button type="submit" className="btn-primary w-full flex items-center justify-center">
                    <Send size={20} className="mr-2" />
                    Send Message
                  </button>
                </form>
              )}
            </div>

            {/* Office Locations */}
            <div className="space-y-8">
              <div>
                <h2 className="text-3xl font-bold text-gray-900 mb-6">Our Offices</h2>
                <p className="text-gray-600 mb-8">
                  Visit us at any of our offices across Kenya for personalized assistance 
                  and property consultations.
                </p>
              </div>
              
              <div className="space-y-6">
                {locations.map((location, index) => (
                  <div key={index} className="bg-white rounded-xl p-6 shadow-lg">
                    <div className="flex items-start space-x-4">
                      <div className="w-12 h-12 bg-primary-100 rounded-lg flex items-center justify-center">
                        <location.icon size={24} className="text-primary-500" />
                      </div>
                      <div className="flex-1">
                        <h3 className="text-lg font-semibold text-gray-900 mb-2">{location.name}</h3>
                        <div className="space-y-1 text-gray-600">
                          <p className="flex items-center">
                            <MapPin size={16} className="mr-2 text-primary-500" />
                            {location.address}
                          </p>
                          <p className="flex items-center">
                            <Phone size={16} className="mr-2 text-primary-500" />
                            <a href={`tel:${location.phone}`} className="hover:text-primary-500">
                              {location.phone}
                            </a>
                          </p>
                          <p className="flex items-center">
                            <Mail size={16} className="mr-2 text-primary-500" />
                            <a href={`mailto:${location.email}`} className="hover:text-primary-500">
                              {location.email}
                            </a>
                          </p>
                        </div>
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div>
      </section>

             {/* Services Section */}
      <section className="bg-primary-50 py-16">
        <div className="container-custom">
          <div className="text-center mb-16">
            <h2 className="text-4xl font-bold text-gray-900 mb-4">How We Can Help You</h2>
            <p className="text-xl text-gray-600 max-w-3xl mx-auto">
              Our team of real estate experts is here to provide comprehensive support 
              for all your property investment needs.
            </p>
          </div>

          <div className="grid md:grid-cols-3 gap-8">
            {services.map((service, index) => (
              <div key={index} className="bg-white rounded-xl p-8 text-center shadow-lg hover:shadow-xl transition-shadow">
                <div className="w-16 h-16 bg-primary-100 rounded-full flex items-center justify-center mx-auto mb-6">
                  <service.icon size={32} className="text-primary-500" />
                </div>
                <h3 className="text-xl font-bold text-gray-900 mb-4">{service.title}</h3>
                <p className="text-gray-600">{service.description}</p>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* FAQ Section */}
      <section className="py-16">
        <div className="container-custom">
          <div className="text-center mb-16">
            <h2 className="text-4xl font-bold text-gray-900 mb-4">Frequently Asked Questions</h2>
            <p className="text-xl text-gray-600 max-w-3xl mx-auto">
              Find answers to common questions about our services and property investment process.
            </p>
          </div>

          <div className="max-w-4xl mx-auto space-y-6">
            <div className="bg-white rounded-xl p-6 shadow-lg">
              <h3 className="text-lg font-semibold text-gray-900 mb-3">
                What documents do I need to purchase land?
              </h3>
              <p className="text-gray-600">
                You'll need a valid ID, proof of income, and we'll guide you through the title deed 
                process. Our team handles all the legal documentation for you.
              </p>
            </div>

            <div className="bg-white rounded-xl p-6 shadow-lg">
              <h3 className="text-lg font-semibold text-gray-900 mb-3">
                Do you offer financing options?
              </h3>
              <p className="text-gray-600">
                Yes, we work with various financial institutions to provide mortgage and financing 
                solutions tailored to your needs and budget.
              </p>
            </div>

            <div className="bg-white rounded-xl p-6 shadow-lg">
              <h3 className="text-lg font-semibold text-gray-900 mb-3">
                Can I schedule a site visit?
              </h3>
              <p className="text-gray-600">
                Absolutely! We offer both in-person and virtual tours. Contact us to schedule 
                a viewing at your preferred time.
              </p>
            </div>
                     </div>
         </div>
       </section>

       {/* Google Maps Section */}
       <section className="py-16 bg-gray-50">
         <div className="container-custom">
           <div className="text-center mb-12">
             <h2 className="text-4xl font-bold text-gray-900 mb-4">Find Our Office</h2>
             <p className="text-xl text-gray-600 max-w-3xl mx-auto">
               Visit us at our head office in Nyali, Mombasa. We're conveniently located and easy to find.
             </p>
           </div>
           
           <div className="max-w-6xl mx-auto">
             <div className="bg-white rounded-2xl shadow-xl overflow-hidden">
               <div className="p-6 border-b">
                 <div className="flex items-center justify-between">
                   <div>
                     <h3 className="text-xl font-bold text-gray-900">Inuka Afrika Properties Limited</h3>
                     <p className="text-gray-600">Head Office - Nyali, Mombasa</p>
                   </div>
                   <div className="text-right">
                     <p className="text-sm text-gray-500">Phone: 0711 02084</p>
                     <p className="text-sm text-gray-500">Email: info@inukaproperties.co.ke</p>
                   </div>
                 </div>
               </div>
               
               <div className="aspect-video w-full">
                 <iframe
                   src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3979.8!2d39.7!3d-4.0!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x0%3A0x0!2zNMKwMDAnMDAuMCJTIDM5wrA0MicwMC4wIkU!5e0!3m2!1sen!2ske!4v1234567890"
                   width="100%"
                   height="100%"
                   style={{ border: 0 }}
                   allowFullScreen=""
                   loading="lazy"
                   referrerPolicy="no-referrer-when-downgrade"
                   title="Inuka Afrika Properties Office Location"
                 ></iframe>
               </div>
               
               <div className="p-6 bg-gray-50">
                 <div className="grid md:grid-cols-2 gap-6">
                   <div>
                     <h4 className="font-semibold text-gray-900 mb-2">Office Address</h4>
                     <p className="text-gray-600">Nyali, Mombasa - Along Links Road, Opposite Kigothos Hotel and Apartment</p>
                   </div>
                   <div>
                     <h4 className="font-semibold text-gray-900 mb-2">Business Hours</h4>
                     <p className="text-gray-600">Mon - Fri: 8:00 AM - 5:00 PM</p>
                     <p className="text-gray-600">Sat: 9:00 AM - 1:00 PM</p>
                   </div>
                 </div>
                 
                 <div className="mt-6 text-center">
                   <a
                     href="https://maps.app.goo.gl/PGMJMW9GuPb8dvKP8"
                     target="_blank"
                     rel="noopener noreferrer"
                     className="inline-flex items-center bg-primary-500 text-white px-6 py-3 rounded-lg hover:bg-primary-600 transition-colors"
                   >
                     <MapPin size={20} className="mr-2" />
                     Open in Google Maps
                   </a>
                 </div>
               </div>
             </div>
           </div>
         </div>
       </section>
     </div>
   );
 };

export default ContactUs;
