import emailjs from '@emailjs/browser';
import { EMAILJS_CONFIG } from '../config/emailjs';

class EmailService {
  constructor() {
    // Initialize EmailJS with your public key
    try {
      emailjs.init(EMAILJS_CONFIG.PUBLIC_KEY);
      console.log('EmailJS initialized successfully with key:', EMAILJS_CONFIG.PUBLIC_KEY);
    } catch (error) {
      console.error('EmailJS initialization failed:', error);
    }
  }

  /**
   * Send booking notification email
   * @param {Object} formData - The booking form data
   * @returns {Promise} - EmailJS response
   */
  async sendBookingNotification(formData) {
    try {
      // Use the most basic template parameters that EmailJS expects
      const templateParams = {
        to_name: 'Inuka Properties Team',
        from_name: formData.name,
        from_email: formData.email,
        from_phone: formData.contact,
        project_interest: formData.projectOfInterest,
        preferred_date: formData.preferredDate,
        preferred_time: formData.preferredTime,
        additional_notes: formData.additionalNotes || 'No additional notes'
      };

      // Send email using EmailJS
      console.log('Sending email with params:', templateParams);
      console.log('Using service:', EMAILJS_CONFIG.SERVICE_ID);
      console.log('Using template:', EMAILJS_CONFIG.TEMPLATE_ID);
      console.log('Using public key:', EMAILJS_CONFIG.PUBLIC_KEY);
      
      const response = await emailjs.send(
        EMAILJS_CONFIG.SERVICE_ID,
        EMAILJS_CONFIG.TEMPLATE_ID,
        templateParams
      );

      console.log('Email sent successfully:', response);
      return { success: true, data: response };
    } catch (error) {
      console.error('Email sending failed:', error);
      console.error('Error details:', {
        message: error.message,
        status: error.status,
        text: error.text
      });
      return { success: false, error: error.message };
    }
  }

  /**
   * Send customer confirmation email
   * @param {Object} formData - The booking form data
   * @returns {Promise} - EmailJS response
   */
  async sendCustomerConfirmation(formData) {
    try {
      const templateParams = {
        to_name: formData.name,
        to_email: formData.email,
        project_interest: formData.projectOfInterest,
        preferred_date: formData.preferredDate,
        preferred_time: formData.preferredTime,
        company_name: EMAILJS_CONFIG.COMPANY_NAME,
        company_phone: EMAILJS_CONFIG.COMPANY_PHONE,
        company_email: EMAILJS_CONFIG.TO_EMAIL
      };

      // You can create a separate template for customer confirmation
      // For now, we'll use the same template but with different parameters
      const response = await emailjs.send(
        EMAILJS_CONFIG.SERVICE_ID,
        EMAILJS_CONFIG.TEMPLATE_ID, // You might want a different template for customers
        templateParams
      );

      console.log('Customer confirmation email sent:', response);
      return { success: true, data: response };
    } catch (error) {
      console.error('Customer confirmation email failed:', error);
      return { success: false, error: error.message };
    }
  }

  /**
   * Test EmailJS connection
   * @returns {Promise<boolean>} - Whether the connection is working
   */
  async testConnection() {
    try {
      // Simple test to check if EmailJS is properly initialized
      if (!EMAILJS_CONFIG.PUBLIC_KEY || EMAILJS_CONFIG.PUBLIC_KEY === 'your_public_key') {
        return false;
      }
      
      // Test with minimal parameters
      const testParams = {
        to_name: 'Test',
        from_name: 'Test User',
        from_email: 'test@example.com',
        from_phone: '123456789',
        project_interest: 'Test Property',
        preferred_date: '2024-01-01',
        preferred_time: '10:00 AM',
        additional_notes: 'Test booking'
      };
      
      console.log('Testing EmailJS connection...');
      const response = await emailjs.send(
        EMAILJS_CONFIG.SERVICE_ID,
        EMAILJS_CONFIG.TEMPLATE_ID,
        testParams
      );
      
      console.log('Test email sent successfully:', response);
      return true;
    } catch (error) {
      console.error('EmailJS test failed:', error);
      return false;
    }
  }
}

// Create and export a single instance
const emailService = new EmailService();
export default emailService;
export { EmailService };
