// Cookie consent utility functions

export const getCookieConsent = () => {
  try {
    const consent = localStorage.getItem('cookieConsent');
    return consent ? JSON.parse(consent) : null;
  } catch (error) {
    console.error('Error reading cookie consent:', error);
    return null;
  }
};

export const hasCookieConsent = (type = 'all') => {
  const consent = getCookieConsent();
  if (!consent) return false;
  
  if (type === 'all') {
    return consent.necessary && consent.functional && consent.analytics && consent.marketing;
  }
  
  return consent[type] || false;
};

export const canUseAnalytics = () => {
  return hasCookieConsent('analytics');
};

export const canUseMarketing = () => {
  return hasCookieConsent('marketing');
};

export const canUseFunctional = () => {
  return hasCookieConsent('functional');
};

export const canUseNecessary = () => {
  return hasCookieConsent('necessary');
};

export const setCookieConsent = (preferences) => {
  try {
    localStorage.setItem('cookieConsent', JSON.stringify(preferences));
    localStorage.setItem('cookieConsentDate', new Date().toISOString());
    return true;
  } catch (error) {
    console.error('Error saving cookie consent:', error);
    return false;
  }
};

export const clearCookieConsent = () => {
  try {
    localStorage.removeItem('cookieConsent');
    localStorage.removeItem('cookieConsentDate');
    return true;
  } catch (error) {
    console.error('Error clearing cookie consent:', error);
    return false;
  }
};

export const getCookieConsentDate = () => {
  try {
    const date = localStorage.getItem('cookieConsentDate');
    return date ? new Date(date) : null;
  } catch (error) {
    console.error('Error reading cookie consent date:', error);
    return null;
  }
};

// Function to check if consent is older than a certain period (e.g., 1 year)
export const isConsentExpired = (maxAgeInDays = 365) => {
  const consentDate = getCookieConsentDate();
  if (!consentDate) return true;
  
  const now = new Date();
  const diffTime = Math.abs(now - consentDate);
  const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
  
  return diffDays > maxAgeInDays;
};

// Function to initialize analytics based on consent
export const initializeAnalytics = () => {
  if (canUseAnalytics()) {
    // Initialize Google Analytics or other analytics tools
    console.log('Analytics cookies enabled - initializing tracking');
    // Add your analytics initialization code here
  } else {
    console.log('Analytics cookies disabled - skipping tracking');
  }
};

// Function to initialize marketing tools based on consent
export const initializeMarketing = () => {
  if (canUseMarketing()) {
    // Initialize marketing tools like Facebook Pixel, Google Ads, etc.
    console.log('Marketing cookies enabled - initializing marketing tools');
    // Add your marketing tool initialization code here
  } else {
    console.log('Marketing cookies disabled - skipping marketing tools');
  }
};

// Function to initialize functional features based on consent
export const initializeFunctional = () => {
  if (canUseFunctional()) {
    // Initialize functional features like personalization, preferences, etc.
    console.log('Functional cookies enabled - initializing enhanced features');
    // Add your functional feature initialization code here
  } else {
    console.log('Functional cookies disabled - using basic features only');
  }
};
