import React from 'react';
import { Download, FileText, Map, Newspaper } from 'lucide-react';

const DocumentDownload = ({ documents, title, type = 'property' }) => {
  const getIcon = (docType) => {
    switch (docType) {
      case 'map':
        return <Map size={20} />;
      case 'newsletter':
        return <Newspaper size={20} />;
      default:
        return <FileText size={20} />;
    }
  };

  const getTypeLabel = (docType) => {
    switch (docType) {
      case 'map':
        return 'Property Map';
      case 'newsletter':
        return 'Newsletter';
      default:
        return 'Document';
    }
  };

  const handleDownload = (doc) => {
    // Create a temporary link element
    const link = document.createElement('a');
    link.href = doc.url;
    link.download = doc.filename || doc.name;
    link.target = '_blank';
    link.rel = 'noopener noreferrer';
    
    // Trigger download
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  if (!documents || documents.length === 0) {
    return null;
  }

  return (
    <div className="bg-white rounded-lg shadow-lg p-6">
      <h3 className="text-xl font-bold text-gray-900 mb-4 flex items-center">
        {type === 'property' ? <Map size={24} className="mr-2 text-primary-500" /> : <Newspaper size={24} className="mr-2 text-primary-500" />}
        {title}
      </h3>
      
      <div className="space-y-3">
        {documents.map((doc, index) => (
          <div 
            key={index}
            className="flex items-center justify-between p-4 border border-gray-200 rounded-lg hover:border-primary-300 hover:bg-primary-50 transition-all duration-200"
          >
            <div className="flex items-center space-x-3">
              <div className="text-primary-500">
                {getIcon(doc.type)}
              </div>
              <div>
                <h4 className="font-semibold text-gray-900">{doc.name}</h4>
                <p className="text-sm text-gray-600">{getTypeLabel(doc.type)}  {doc.size}</p>
              </div>
            </div>
            
            <button
              onClick={() => handleDownload(doc)}
              className="flex items-center space-x-2 bg-primary-500 hover:bg-primary-600 text-white px-4 py-2 rounded-lg transition-colors duration-200"
            >
              <Download size={16} />
              <span className="text-sm font-medium">Download</span>
            </button>
          </div>
        ))}
      </div>
      
      <p className="text-sm text-gray-500 mt-4">
        Click on any document above to download it to your device.
      </p>
    </div>
  );
};

export default DocumentDownload;
