import React, { useState, useRef } from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import { X, Send, Bot } from 'lucide-react';
import Header from './components/Header';
import Footer from './components/Footer';
import HomePage from './pages/HomePage';
import LandForSale from './pages/LandForSale';
import AboutUs from './pages/AboutUs';
import WhoWeAre from './pages/WhoWeAre';
import WhyUs from './pages/WhyUs';
import OurTeam from './pages/OurTeam';
import CSR from './pages/CSR';
import OurPartners from './pages/OurPartners';
import IAPLInsider from './pages/IAPLInsider';
import Blogs from './pages/Blogs';
import News from './pages/News';
import MarketResearch from './pages/MarketResearch';
import Testimonials from './pages/Testimonials';
import Downloads from './pages/Downloads';
import ContactUs from './pages/ContactUs';
import PropertyDetail from './pages/PropertyDetail';
import PrivacyPolicy from './pages/PrivacyPolicy';
import CookiePolicy from './pages/CookiePolicy';
import CookieConsent from './components/CookieConsent';

function App() {
  // Chat bot state
  const [isChatOpen, setIsChatOpen] = useState(false);
  const [messages, setMessages] = useState([
    {
      id: 1,
      type: 'bot',
      content: 'Hello! I\'m IAPL, your virtual assistant. I can help you with information about our land plots, payment plans, locations, and more. How can I assist you today?',
      timestamp: new Date()
    }
  ]);
  const [inputMessage, setInputMessage] = useState('');
  const [isCollectingContact, setIsCollectingContact] = useState(false);
  const [contactInfo, setContactInfo] = useState({ name: '', email: '', phone: '' });
  const chatEndRef = useRef(null);

  // Chat bot functions
  const scrollToBottom = () => {
    chatEndRef.current?.scrollIntoView({ behavior: "smooth" });
  };

  React.useEffect(() => {
    scrollToBottom();
  }, [messages]);

  const handleSendMessage = () => {
    if (inputMessage.trim() === '') return;

    const userMessage = {
      id: messages.length + 1,
      type: 'user',
      content: inputMessage,
      timestamp: new Date()
    };

    setMessages(prev => [...prev, userMessage]);
    setInputMessage('');

    // Simulate bot response
    setTimeout(() => {
      const botResponse = generateBotResponse(inputMessage.toLowerCase());
      const botMessage = {
        id: messages.length + 2,
        type: 'bot',
        content: botResponse,
        timestamp: new Date()
      };
      setMessages(prev => [...prev, botMessage]);
    }, 1000);
  };

  const generateBotResponse = (message) => {
    const lowerMessage = message.toLowerCase();
    
    // Company Information
    if (lowerMessage.includes('company') || lowerMessage.includes('about') || lowerMessage.includes('who')) {
      return 'Inuka Afrika Properties Limited (IAPL) is a legally registered limited liability company based in Nyali, Mombasa. We operate in coastal Kenya and Nairobi outskirts, specializing in land and affordable housing. We offer a complete package of real estate investment solutions including sales, rentals, retail, commercial, mortgages, new development marketing, property management, and title issuance.';
    }
    
    // Mission and Vision
    if (lowerMessage.includes('mission') || lowerMessage.includes('vision') || lowerMessage.includes('purpose')) {
      return 'Our mission is to provide affordable land investment opportunities with secure title deeds. Our vision is to be the leading real estate company in coastal Kenya, offering genuine land plots with excellent return on investment. We focus on plots of land, not houses, providing flexible payment plans with no interest.';
    }
    
    // Pricing Information
    if (lowerMessage.includes('price') || lowerMessage.includes('cost') || lowerMessage.includes('how much')) {
      return 'Our land plots start from KES 199,000. We offer flexible payment plans with no interest. You can pay in installments over 6-12 months. The deposit starts from KES 50,000. Would you like to know about specific locations or payment terms?';
    }
    
    // Location Information
    if (lowerMessage.includes('location') || lowerMessage.includes('where') || lowerMessage.includes('area')) {
      return 'We have land plots in strategic locations including Kilifi, Malindi, Mtwapa, Mariakani, Bofa, Tezo, Chumani, and Msabaha. Our projects include Chumani Phase 3, Mida Park View Phase 1, Ocean View Gardens, Kikambala Gardens Phase 2, Mtondia Highway Gardens, Chumani Phase 6, Bofa Phase 9 Extension, Bofa Platinum Estate, Malindi Airport View Gardens, and Msabaha Phase 6. Which area interests you?';
    }
    
    // Payment Plans
    if (lowerMessage.includes('payment') || lowerMessage.includes('installment') || lowerMessage.includes('plan')) {
      return 'We offer flexible payment plans with no interest. You can pay in installments over 6-12 months. The deposit starts from KES 50,000. We also offer value addition services like fencing, access roads, and bush clearing. Would you like more details about a specific project?';
    }
    
    // Title Deeds
    if (lowerMessage.includes('title deed') || lowerMessage.includes('document') || lowerMessage.includes('legal')) {
      return 'All our properties come with genuine title deeds. Our title deed processing is fast and efficient. We handle all legal documentation for you, including stamp duty and transfer processes. We have disbursed over 4,513 title deeds to satisfied customers.';
    }
    
    // Contact Information
    if (lowerMessage.includes('contact') || lowerMessage.includes('phone') || lowerMessage.includes('call')) {
      return 'You can contact us at 0711 02084 or visit our office in Nyali, along Links Road opposite Kigothos Hotel and Apartment. Our office hours are 8am-5pm Monday-Friday, 9am-1pm Saturday. Would you like me to collect your contact information so our team can reach out to you?';
    }
    
    // Office Location
    if (lowerMessage.includes('office') || lowerMessage.includes('address')) {
      return 'Our office is located in Nyali, along Links Road opposite Kigothos Hotel and Apartment. We are open 8am-5pm Monday-Friday, and 9am-1pm on Saturdays.';
    }
    
    // Site Visits
    if (lowerMessage.includes('visit') || lowerMessage.includes('view') || lowerMessage.includes('see')) {
      return 'We offer site visits to all our properties. You can book a viewing by calling 0711 02084 or using our booking form. Would you like to schedule a visit? I can also collect your contact information so our team can arrange a site visit for you.';
    }
    
    // Company Stats
    if (lowerMessage.includes('experience') || lowerMessage.includes('years') || lowerMessage.includes('projects')) {
      return 'We have 9+ years of experience in land investment, completed 147+ projects, disbursed 4,513+ title deeds, and have a workforce of 200+ professionals. Our track record speaks for itself!';
    }
    
    // Specific Properties
    if (lowerMessage.includes('chumani')) {
      return 'Chumani Phase 3 offers 1/8 acre plots at KES 550,000 with water and electricity available. Chumani Phase 6 is also available. Both are ideal for residential development with secure title deeds. Would you like more details?';
    }
    if (lowerMessage.includes('mida')) {
      return 'Mida Park View Phase 1 offers 1/8 acre plots at KES 350,000, located just 800 meters from the Kilifi-Malindi highway. Features include park view, access road, security, and perimeter fence.';
    }
    if (lowerMessage.includes('ocean view') || lowerMessage.includes('tezo')) {
      return 'Ocean View Gardens in Kilifi, Tezo offers 1/8 acre plots at KES 495,000. Located 2.5km from Kilifi-Mombasa Highway and just 1km to the beach. Features include ocean view, beach access, water, and electricity.';
    }
    if (lowerMessage.includes('bofa')) {
      return 'We have Bofa Phase 9 Extension and Bofa Platinum Estate. Both offer excellent investment opportunities with flexible payment plans and secure title deeds. Would you like specific pricing and features?';
    }
    if (lowerMessage.includes('malindi')) {
      return 'Malindi Airport View Gardens offers plots with excellent proximity to Malindi Airport. We also have other Malindi projects. Would you like specific details about our Malindi properties?';
    }
    
    // Services
    if (lowerMessage.includes('service') || lowerMessage.includes('what do you do')) {
      return 'We offer land plot sales, land development services, and title services. Our core business is land and affordable housing, providing a complete package of real estate investment solutions including sales, rentals, retail, commercial, mortgages, new development marketing, property management, and title issuance.';
    }
    
    // Investment Benefits
    if (lowerMessage.includes('investment') || lowerMessage.includes('return') || lowerMessage.includes('benefit')) {
      return 'Our land plots offer excellent investment potential with guaranteed returns. All projects are in strategic locations, come with genuine title deeds, are affordable starting from KES 199,000, offer flexible payment plans with no interest, and include value addition services.';
    }
    
    // Greetings
    if (lowerMessage.includes('hello') || lowerMessage.includes('hi') || lowerMessage.includes('hey')) {
      return 'Hello! How can I help you with your land investment today? I can provide information about our plots, locations, prices, payment plans, and company details. Would you like to know about any specific aspect of our services?';
    }
    
    // Contact Collection Trigger
    if (lowerMessage.includes('contact me') || lowerMessage.includes('call me') || lowerMessage.includes('reach out') || lowerMessage.includes('get back')) {
      setIsCollectingContact(true);
      return 'I\'d be happy to have our team contact you! Please provide your name, email, and phone number so we can reach out with more information.';
    }
    
    // Default response with contact collection offer
    return 'Thank you for your question. I can provide detailed information about our land plots, locations, prices, payment plans, and company services. For specific inquiries or to have our team contact you directly, please call us at 0711 02084 or would you like me to collect your contact information so we can reach out to you?';
  };

  const handleKeyPress = (e) => {
    if (e.key === 'Enter') {
      if (isCollectingContact) {
        handleContactSubmit();
      } else {
        handleSendMessage();
      }
    }
  };

  const handleContactSubmit = () => {
    if (contactInfo.name && contactInfo.email && contactInfo.phone) {
      const contactMessage = {
        id: messages.length + 1,
        type: 'user',
        content: `Contact Info: ${contactInfo.name}, ${contactInfo.email}, ${contactInfo.phone}`,
        timestamp: new Date()
      };
      
      setMessages(prev => [...prev, contactMessage]);
      setContactInfo({ name: '', email: '', phone: '' });
      setIsCollectingContact(false);
      
      // Bot confirmation
      setTimeout(() => {
        const botMessage = {
          id: messages.length + 2,
          type: 'bot',
          content: `Thank you ${contactInfo.name}! I've recorded your contact information. Our team will reach out to you at ${contactInfo.email} or call you at ${contactInfo.phone} within 24 hours with detailed information about our land investment opportunities. Is there anything specific you'd like to know while you wait?`,
          timestamp: new Date()
        };
        setMessages(prev => [...prev, botMessage]);
      }, 1000);
    }
  };

  const handleContactInputChange = (field, value) => {
    setContactInfo(prev => ({ ...prev, [field]: value }));
  };

  return (
    <Router>
      <div className="min-h-screen bg-gray-50">
        <Header />
        <main>
          <Routes>
            <Route path="/" element={<HomePage />} />
            <Route path="/land-for-sale" element={<LandForSale />} />
            <Route path="/land-for-sale/:id" element={<PropertyDetail />} />
            <Route path="/about-us" element={<AboutUs />} />
            <Route path="/about-us/who-we-are" element={<WhoWeAre />} />
            <Route path="/about-us/why-us" element={<WhyUs />} />
            <Route path="/about-us/our-team" element={<OurTeam />} />
            <Route path="/about-us/csr" element={<CSR />} />
            <Route path="/about-us/our-partners" element={<OurPartners />} />
            <Route path="/iapl-insider" element={<IAPLInsider />} />
            <Route path="/iapl-insider/blogs" element={<Blogs />} />
            <Route path="/iapl-insider/news" element={<News />} />
            <Route path="/iapl-insider/market-research" element={<MarketResearch />} />
            <Route path="/testimonials" element={<Testimonials />} />
            <Route path="/downloads" element={<Downloads />} />
            <Route path="/contact-us" element={<ContactUs />} />
            <Route path="/privacy-policy" element={<PrivacyPolicy />} />
            <Route path="/cookie-policy" element={<CookiePolicy />} />
          </Routes>
        </main>
        <Footer />

        {/* Floating WhatsApp Button - Available on all pages */}
        <a 
          href="https://wa.me/25471102084?text=Hello, I'm interested in land investment opportunities with Inuka Properties. Please provide more information about available plots and payment plans."
          target="_blank"
          rel="noopener noreferrer"
          className="fixed bottom-6 right-6 z-50 bg-secondary-500 hover:bg-secondary-600 text-white rounded-lg px-6 py-3 shadow-lg transition-all duration-300 transform hover:scale-105 hover:shadow-xl flex items-center space-x-2 font-semibold"
          style={{
            boxShadow: '0 10px 25px rgba(0, 0, 0, 0.2)'
          }}
        >
          <svg className="w-5 h-5" fill="currentColor" viewBox="0 0 24 24">
            <path d="M17.472 14.382c-.297-.149-1.758-.867-2.03-.967-.273-.099-.471-.148-.67.15-.197.297-.767.966-.94 1.164-.173.199-.347.223-.644.075-.297-.15-1.255-.463-2.39-1.475-.883-.788-1.48-1.761-1.653-2.059-.173-.297-.018-.458.13-.606.134-.133.298-.347.446-.52.149-.174.198-.298.298-.497.099-.198.05-.371-.025-.52-.075-.149-.669-1.612-.916-2.207-.242-.579-.487-.5-.669-.51-.173-.008-.371-.01-.57-.01-.198 0-.52.074-.792.372-.272.297-1.04 1.016-1.04 2.479 0 1.462 1.065 2.875 1.213 3.074.149.198 2.096 3.2 5.077 4.487.709.306 1.262.489 1.694.625.712.227 1.36.195 1.871.118.571-.085 1.758-.719 2.006-1.413.248-.694.248-1.289.173-1.413-.074-.124-.272-.198-.57-.347m-5.421 7.403h-.004a9.87 9.87 0 01-5.031-1.378l-.361-.214-3.741.982.998-3.648-.235-.374a9.86 9.86 0 01-1.51-5.26c.001-5.45 4.436-9.884 9.888-9.884 2.64 0 5.122 1.03 6.988 2.898a9.825 9.825 0 012.893 6.994c-.003 5.45-4.437 9.884-9.885 9.884m8.413-18.297A11.815 11.815 0 0012.05 0C5.495 0 .16 5.335.157 11.892c0 2.096.547 4.142 1.588 5.945L.057 24l6.305-1.654a11.882 11.882 0 005.683 1.448h.005c6.554 0 11.89-5.335 11.893-11.893A11.821 11.821 0 0020.885 3.488"/>
          </svg>
          <span>Enquire now</span>
        </a>

        {/* IAPL Chat Widget - Available on all pages */}
        <div className="fixed left-6 bottom-6 z-50">
          {/* Chat Toggle Button */}
          {!isChatOpen && (
            <button
              onClick={() => setIsChatOpen(true)}
              className="bg-primary-500 hover:bg-primary-600 text-white rounded-lg px-4 py-3 shadow-lg transition-all duration-300 transform hover:scale-110 hover:shadow-xl flex items-center space-x-2 font-semibold"
              style={{
                boxShadow: '0 10px 25px rgba(0, 0, 0, 0.2)'
              }}
            >
              <Bot size={20} />
              <span>Talk to us</span>
            </button>
          )}

          {/* Chat Window */}
          {isChatOpen && (
            <div className="bg-white rounded-lg shadow-2xl w-80 h-96 flex flex-col border border-gray-200">
              {/* Chat Header */}
              <div className="bg-primary-500 text-white p-4 rounded-t-lg flex items-center justify-between">
                <div className="flex items-center space-x-2">
                  <Bot size={20} />
                  <span className="font-semibold">IAPL</span>
                </div>
                <button
                  onClick={() => setIsChatOpen(false)}
                  className="text-white hover:text-gray-200 transition-colors"
                >
                  <X size={20} />
                </button>
              </div>

              {/* Chat Messages */}
              <div className="flex-1 overflow-y-auto p-4 space-y-4">
                {messages.map((message) => (
                  <div
                    key={message.id}
                    className={`flex ${message.type === 'user' ? 'justify-end' : 'justify-start'}`}
                  >
                    <div
                      className={`max-w-xs px-4 py-2 rounded-lg ${
                        message.type === 'user'
                          ? 'bg-primary-500 text-white'
                          : 'bg-gray-100 text-gray-800'
                      }`}
                    >
                      <p className="text-sm">{message.content}</p>
                      <p className="text-xs opacity-70 mt-1">
                        {message.timestamp.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}
                      </p>
                    </div>
                  </div>
                ))}
                <div ref={chatEndRef} />
              </div>

              {/* Chat Input */}
              <div className="p-4 border-t border-gray-200">
                {isCollectingContact ? (
                  <div className="space-y-3">
                    <p className="text-sm text-gray-600 mb-2">Please provide your contact information:</p>
                    <input
                      type="text"
                      value={contactInfo.name}
                      onChange={(e) => handleContactInputChange('name', e.target.value)}
                      onKeyPress={handleKeyPress}
                      placeholder="Your full name"
                      className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent text-sm"
                    />
                    <input
                      type="email"
                      value={contactInfo.email}
                      onChange={(e) => handleContactInputChange('email', e.target.value)}
                      onKeyPress={handleKeyPress}
                      placeholder="Your email address"
                      className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent text-sm"
                    />
                    <input
                      type="tel"
                      value={contactInfo.phone}
                      onChange={(e) => handleContactInputChange('phone', e.target.value)}
                      onKeyPress={handleKeyPress}
                      placeholder="Your phone number"
                      className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent text-sm"
                    />
                    <div className="flex space-x-2">
                      <button
                        onClick={handleContactSubmit}
                        disabled={!contactInfo.name || !contactInfo.email || !contactInfo.phone}
                        className="flex-1 bg-primary-500 hover:bg-primary-600 disabled:bg-gray-300 text-white py-2 rounded-lg transition-colors text-sm font-medium"
                      >
                        Submit Contact Info
                      </button>
                      <button
                        onClick={() => setIsCollectingContact(false)}
                        className="px-4 py-2 border border-gray-300 text-gray-600 rounded-lg hover:bg-gray-50 transition-colors text-sm"
                      >
                        Cancel
                      </button>
                    </div>
                  </div>
                ) : (
                  <div className="flex space-x-2">
                    <input
                      type="text"
                      value={inputMessage}
                      onChange={(e) => setInputMessage(e.target.value)}
                      onKeyPress={handleKeyPress}
                      placeholder="Type your message..."
                      className="flex-1 px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent"
                    />
                    <button
                      onClick={handleSendMessage}
                      disabled={inputMessage.trim() === ''}
                      className="bg-primary-500 hover:bg-primary-600 disabled:bg-gray-300 text-white p-2 rounded-lg transition-colors"
                    >
                      <Send size={16} />
                    </button>
                  </div>
                )}
              </div>
            </div>
          )}
        </div>

        {/* Cookie Consent Banner - Available on all pages */}
        <CookieConsent />
      </div>
    </Router>
  );
}

export default App;
