import React from 'react';
import { Users, Target, Eye, Heart } from 'lucide-react';

const WhoWeAre = () => {
  const values = [
    {
      icon: Heart,
      title: "Integrity",
      description: "We conduct business with honesty, transparency, and ethical practices."
    },
    {
      icon: Target,
      title: "Excellence",
      description: "We strive for excellence in every aspect of our service delivery."
    },
    {
      icon: Users,
      title: "Customer Focus",
      description: "Our customers are at the heart of everything we do."
    },
    {
      icon: Eye,
      title: "Innovation",
      description: "We embrace new technologies and approaches to serve our clients better."
    }
  ];

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Hero Section */}
      <section className="bg-gradient-to-r from-primary-700 to-primary-800 text-white py-20">
        <div className="container-custom text-center">
          <h1 className="text-5xl font-bold mb-6">Who We Are</h1>
          <p className="text-xl text-gray-200 max-w-3xl mx-auto">
            Discover the story behind Inuka Properties, our mission, vision, and the values 
            that drive us to help families achieve their property ownership dreams.
          </p>
        </div>
      </section>

      {/* Company Story */}
      <section className="py-16">
        <div className="container-custom">
          <div className="grid lg:grid-cols-2 gap-16 items-center">
            <div>
              <h2 className="text-4xl font-bold text-gray-900 mb-6">Our Story</h2>
              <div className="space-y-4 text-lg text-gray-600">
                <p>
                  Inuka Afrika Properties Limited (IAPL) was founded with a simple yet powerful 
                  vision: to make property ownership accessible to every Kenyan family. What 
                  started as a small real estate agency in Mombasa has grown into one of the 
                  most trusted names in coastal Kenya's property market.
                </p>
                <p>
                  Our journey began in 2009 when we recognized the growing need for affordable, 
                  quality land investments in rapidly developing areas. We saw the potential in 
                  coastal Kenya and committed ourselves to bringing the best investment 
                  opportunities to our clients.
                </p>
                <p>
                  Today, we're proud to have helped over 500 families achieve their property 
                  ownership dreams, with a presence spanning from Mariakani to Malindi, and 
                  now extending to Nairobi outskirts.
                </p>
              </div>
            </div>
            
            <div className="bg-white rounded-2xl shadow-xl p-8">
              <h3 className="text-2xl font-bold text-gray-900 mb-6">Company Timeline</h3>
              <div className="space-y-6">
                <div className="flex items-start space-x-4">
                  <div className="w-12 h-12 bg-primary-100 rounded-full flex items-center justify-center flex-shrink-0">
                    <span className="text-primary-600 font-bold">2009</span>
                  </div>
                  <div>
                    <h4 className="font-semibold text-gray-900 mb-1">Company Founded</h4>
                    <p className="text-gray-600 text-sm">Started operations in Mombasa with focus on coastal properties</p>
                  </div>
                </div>
                
                <div className="flex items-start space-x-4">
                  <div className="w-12 h-12 bg-primary-100 rounded-full flex items-center justify-center flex-shrink-0">
                    <span className="text-primary-600 font-bold">2015</span>
                  </div>
                  <div>
                    <h4 className="font-semibold text-gray-900 mb-1">Expansion Phase</h4>
                    <p className="text-gray-600 text-sm">Extended operations to Kilifi and Malindi regions</p>
                  </div>
                </div>
                
                <div className="flex items-start space-x-4">
                  <div className="w-12 h-12 bg-primary-100 rounded-full flex items-center justify-center flex-shrink-0">
                    <span className="text-primary-600 font-bold">2019</span>
                  </div>
                  <div>
                    <h4 className="font-semibold text-gray-900 mb-1">Nairobi Expansion</h4>
                    <p className="text-gray-600 text-sm">Entered Nairobi market with strategic land developments</p>
                  </div>
                </div>
                
                <div className="flex items-start space-x-4">
                  <div className="w-12 h-12 bg-primary-100 rounded-full flex items-center justify-center flex-shrink-0">
                    <span className="text-primary-600 font-bold">2024</span>
                  </div>
                  <div>
                    <h4 className="font-semibold text-gray-900 mb-1">Digital Innovation</h4>
                    <p className="text-gray-600 text-sm">Launched virtual tours and enhanced online services</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* Mission & Vision */}
      <section className="py-16 bg-white">
        <div className="container-custom">
          <div className="grid md:grid-cols-2 gap-16">
            <div className="text-center">
              <div className="w-20 h-20 bg-primary-100 rounded-full flex items-center justify-center mx-auto mb-6">
                <Target size={40} className="text-primary-600" />
              </div>
              <h3 className="text-3xl font-bold text-gray-900 mb-6">Our Mission</h3>
              <p className="text-lg text-gray-600 leading-relaxed">
                To provide affordable, secure, and strategically located land investments 
                that enable Kenyan families to build wealth and secure their future through 
                property ownership. We are committed to making the dream of owning land 
                accessible to everyone.
              </p>
            </div>
            
            <div className="text-center">
              <div className="w-20 h-20 bg-accent-100 rounded-full flex items-center justify-center mx-auto mb-6">
                <Eye size={40} className="text-accent-600" />
              </div>
              <h3 className="text-3xl font-bold text-gray-900 mb-6">Our Vision</h3>
              <p className="text-lg text-gray-600 leading-relaxed">
                To be the leading real estate company in East Africa, known for integrity, 
                innovation, and customer satisfaction. We envision a future where every 
                Kenyan family has access to quality property investments that contribute 
                to national development and individual prosperity.
              </p>
            </div>
          </div>
        </div>
      </section>

      {/* Core Values */}
      <section className="py-16 bg-gray-50">
        <div className="container-custom">
          <div className="text-center mb-16">
            <h2 className="text-4xl font-bold text-gray-900 mb-4">Our Core Values</h2>
            <p className="text-xl text-gray-600 max-w-3xl mx-auto">
              These fundamental principles guide every decision we make and every action we take 
              in serving our clients and communities.
            </p>
          </div>

          <div className="grid md:grid-cols-2 lg:grid-cols-4 gap-8">
            {values.map((value, index) => (
              <div key={index} className="bg-white rounded-xl p-6 text-center shadow-lg hover:shadow-xl transition-shadow">
                <div className="w-16 h-16 bg-primary-100 rounded-full flex items-center justify-center mx-auto mb-4">
                  <value.icon size={32} className="text-primary-600" />
                </div>
                <h3 className="text-xl font-bold text-gray-900 mb-2">{value.title}</h3>
                <p className="text-gray-600">{value.description}</p>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* CTA Section */}
      <section className="bg-gradient-to-r from-primary-600 to-primary-700 text-white py-16">
        <div className="container-custom text-center">
          <h2 className="text-4xl font-bold mb-6">Ready to Work With Us?</h2>
          <p className="text-xl text-gray-200 mb-8 max-w-2xl mx-auto">
            Join hundreds of families who have already trusted Inuka Properties with their 
            real estate investments. Let us help you achieve your property ownership dreams.
          </p>
          <div className="flex flex-col sm:flex-row gap-4 justify-center">
            <a href="/contact-us" className="btn-secondary text-lg px-8 py-4">
              Book a Site Visit
            </a>
            <a href="/land-for-sale" className="btn-outline border-white text-white hover:bg-white hover:text-primary-600 text-lg px-8 py-4">
              View Properties
            </a>
          </div>
        </div>
      </section>
    </div>
  );
};

export default WhoWeAre;
