import React, { useState } from 'react';
import { Star, Quote, MapPin, Calendar, User, Filter } from 'lucide-react';

const Testimonials = () => {
  const [selectedLocation, setSelectedLocation] = useState('');
  const [selectedRating, setSelectedRating] = useState('');

  const testimonials = [
    {
      id: 1,
      name: "Sarah Mwangi",
      role: "Property Owner",
      location: "Chumani Phase 3",
      rating: 5,
      date: "December 2024",
      content: "Inuka Properties helped me find the perfect plot in Chumani. Their team was professional and the process was smooth from start to finish. The virtual tour feature helped me make an informed decision without having to travel multiple times."
    },
    {
      id: 2,
      name: "John Kamau",
      role: "Investor",
      location: "Bofa Platinum Estate",
      rating: 5,
      date: "November 2024",
      content: "I've invested in multiple properties through Inuka Properties. Their market knowledge and customer service are exceptional. They helped me understand the investment potential of each location and made the entire process transparent."
    },
    {
      id: 3,
      name: "Mary Wanjiku",
      role: "Home Buyer",
      location: "Mida Park View Phase 1",
      rating: 5,
      date: "October 2024",
      content: "The virtual tour feature helped me make an informed decision. I'm now a proud owner of a plot in Mida Park View. The team was patient and answered all my questions about the development and surrounding amenities."
    },
    {
      id: 4,
      name: "David Ochieng",
      role: "First-time Buyer",
      location: "Kikambala Gardens Phase 2",
      rating: 5,
      date: "September 2024",
      content: "As a first-time property buyer, I was nervous about the process. Inuka Properties guided me through every step, from understanding the legal requirements to securing financing. They made my dream of owning land a reality."
    },
    {
      id: 5,
      name: "Grace Akinyi",
      role: "Retiree",
      location: "Ocean View Gardens",
      rating: 5,
      date: "August 2024",
      content: "I wanted to invest my retirement savings in a property that would provide both security and potential returns. Inuka Properties helped me find the perfect plot in Ocean View Gardens. The location is perfect for my retirement plans."
    },
    {
      id: 6,
      name: "Peter Muthoni",
      role: "Business Owner",
      location: "Mtondia Highway Gardens",
      rating: 5,
      date: "July 2024",
      content: "I needed a strategic location for my business expansion. Inuka Properties helped me find a plot along the highway with excellent connectivity. The investment has already shown great returns as the area develops rapidly."
    },
    {
      id: 7,
      name: "Jane Njeri",
      role: "Family Buyer",
      location: "Chumani Phase 6",
      rating: 5,
      date: "June 2024",
      content: "We wanted to build a family home in a safe, developing area. Chumani Phase 6 offered everything we needed - security, community facilities, and excellent infrastructure. Inuka Properties made the entire process family-friendly."
    },
    {
      id: 8,
      name: "Michael Odhiambo",
      role: "Property Developer",
      location: "Bofa Phase 9 Extension",
      rating: 5,
      date: "May 2024",
      content: "As a property developer, I appreciate Inuka Properties' attention to detail and quality. The Bofa Phase 9 Extension offers excellent infrastructure and planning. I've already started developing multiple units and the market response is excellent."
    }
  ];

  const locations = ["All Locations", "Chumani", "Bofa", "Mida", "Kikambala", "Mtondia", "Ocean View", "Malindi", "Msabaha"];

  const filteredTestimonials = testimonials.filter(testimonial => {
    if (selectedLocation && selectedLocation !== "All Locations" && testimonial.location !== selectedLocation) {
      return false;
    }
    if (selectedRating && testimonial.rating !== parseInt(selectedRating)) {
      return false;
    }
    return true;
  });

  const renderStars = (rating) => {
    return Array.from({ length: 5 }, (_, index) => (
      <Star
        key={index}
        size={20}
        className={`${
          index < rating ? 'text-yellow-400 fill-current' : 'text-gray-300'
        }`}
      />
    ));
  };

  const stats = [
    { number: "500+", label: "Happy Clients" },
    { number: "4.9", label: "Average Rating" },
    { number: "15+", label: "Years Experience" },
    { number: "100%", label: "Satisfaction Rate" }
  ];

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Hero Section */}
      <section className="bg-gradient-to-r from-primary-700 to-primary-800 text-white py-20">
        <div className="container-custom text-center">
          <h1 className="text-5xl font-bold mb-6">Client Testimonials</h1>
          <p className="text-xl text-gray-200 max-w-3xl mx-auto">
            Hear from our satisfied clients about their experience with Inuka Properties. 
            Real stories from real people who have achieved their property ownership dreams.
          </p>
        </div>
      </section>

      {/* Stats Section */}
      <section className="bg-white py-16">
        <div className="container-custom">
          <div className="grid grid-cols-2 lg:grid-cols-4 gap-8">
            {stats.map((stat, index) => (
              <div key={index} className="text-center">
                <div className="text-4xl font-bold text-primary-600 mb-2">{stat.number}</div>
                <div className="text-gray-600">{stat.label}</div>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* Filters Section */}
      <section className="bg-white border-b py-8">
        <div className="container-custom">
          <div className="flex flex-col md:flex-row gap-6 items-center justify-between">
            <div className="flex items-center gap-4">
              <Filter size={20} className="text-gray-600" />
              <span className="text-gray-700 font-medium">Filter by:</span>
            </div>
            
            <div className="flex gap-4">
              <select
                value={selectedLocation}
                onChange={(e) => setSelectedLocation(e.target.value)}
                className="px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent"
              >
                {locations.map((location) => (
                  <option key={location} value={location}>
                    {location}
                  </option>
                ))}
              </select>
              
              <select
                value={selectedRating}
                onChange={(e) => setSelectedRating(e.target.value)}
                className="px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent"
              >
                <option value="">All Ratings</option>
                <option value="5">5 Stars</option>
                <option value="4">4 Stars</option>
                <option value="3">3 Stars</option>
              </select>
            </div>
          </div>
        </div>
      </section>

      {/* Testimonials Grid */}
      <section className="py-16">
        <div className="container-custom">
          {filteredTestimonials.length > 0 ? (
            <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-8">
              {filteredTestimonials.map((testimonial) => (
                <div key={testimonial.id} className="bg-white rounded-xl shadow-lg p-6 hover:shadow-xl transition-shadow">
                  {/* Header */}
                  <div className="flex items-start space-x-4 mb-6">
                    <div className="w-16 h-16 rounded-full bg-primary-100 flex items-center justify-center flex-shrink-0">
                      <User size={24} className="text-primary-600" />
                    </div>
                    <div className="flex-1">
                      <h3 className="font-semibold text-gray-900">{testimonial.name}</h3>
                      <p className="text-sm text-gray-600">{testimonial.role}</p>
                      <div className="flex items-center space-x-1 mt-2">
                        {renderStars(testimonial.rating)}
                      </div>
                    </div>
                  </div>

                  {/* Quote */}
                  <div className="mb-6">
                    <Quote size={20} className="text-primary-400 mb-2" />
                    <p className="text-gray-700 leading-relaxed italic">
                      "{testimonial.content}"
                    </p>
                  </div>

                  {/* Footer */}
                  <div className="flex items-center justify-between text-sm text-gray-500">
                    <div className="flex items-center space-x-2">
                      <MapPin size={16} />
                      <span>{testimonial.location}</span>
                    </div>
                    <div className="flex items-center space-x-2">
                      <Calendar size={16} />
                      <span>{testimonial.date}</span>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          ) : (
            <div className="text-center py-16">
              <div className="w-24 h-24 bg-gray-100 rounded-full flex items-center justify-center mx-auto mb-6">
                <User size={48} className="text-gray-400" />
              </div>
              <h3 className="text-2xl font-bold text-gray-900 mb-4">No Testimonials Found</h3>
              <p className="text-gray-600">
                No testimonials match your current filters. Try adjusting your criteria.
              </p>
            </div>
          )}
        </div>
      </section>

      {/* CTA Section */}
      <section className="bg-primary-50 py-16">
        <div className="container-custom text-center">
          <h2 className="text-4xl font-bold text-gray-900 mb-6">Join Our Happy Clients</h2>
          <p className="text-xl text-gray-600 mb-8 max-w-2xl mx-auto">
            Ready to start your property investment journey? Let us help you find the perfect 
            land investment and join the hundreds of satisfied clients who trust Inuka Properties.
          </p>
          <div className="flex flex-col sm:flex-row gap-4 justify-center">
            <a href="tel:+254700000000" className="btn-primary text-lg px-8 py-4">
              Call Us Now
            </a>
            <a href="/contact-us" className="btn-outline text-lg px-8 py-4">
              Contact Us
            </a>
          </div>
        </div>
      </section>

      {/* Trust Indicators */}
      <section className="py-16 bg-white">
        <div className="container-custom">
          <div className="text-center mb-12">
            <h2 className="text-3xl font-bold text-gray-900 mb-4">Why Clients Trust Us</h2>
            <p className="text-xl text-gray-600 max-w-3xl mx-auto">
              Our commitment to transparency, quality, and customer satisfaction has made us 
              the trusted choice for real estate investments in Kenya.
            </p>
          </div>
          
          <div className="grid md:grid-cols-3 gap-8">
            <div className="text-center">
              <div className="w-16 h-16 bg-primary-100 rounded-full flex items-center justify-center mx-auto mb-4">
                <Quote size={32} className="text-primary-600" />
              </div>
              <h3 className="text-xl font-bold text-gray-900 mb-2">Transparent Process</h3>
              <p className="text-gray-600">
                We believe in complete transparency throughout the property acquisition process.
              </p>
            </div>
            
            <div className="text-center">
              <div className="w-16 h-16 bg-primary-100 rounded-full flex items-center justify-center mx-auto mb-4">
                <Star size={32} className="text-primary-600" />
              </div>
              <h3 className="text-xl font-bold text-gray-900 mb-2">Quality Assurance</h3>
              <p className="text-gray-600">
                Every property we offer meets our strict quality and legal compliance standards.
              </p>
            </div>
            
            <div className="text-center">
              <div className="w-16 h-16 bg-primary-100 rounded-full flex items-center justify-center mx-auto mb-4">
                <User size={32} className="text-primary-600" />
              </div>
              <h3 className="text-xl font-bold text-gray-900 mb-2">Customer Support</h3>
              <p className="text-gray-600">
                Our dedicated team provides ongoing support even after property acquisition.
              </p>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
};

export default Testimonials;
