import React from 'react';
import { MapPin, Ruler, Tag, Eye, CheckCircle, Download } from 'lucide-react';
import { Link } from 'react-router-dom';

const PropertyCard = ({ property }) => {
  const handleDownloadMap = (e) => {
    e.preventDefault();
    e.stopPropagation();
    if (property.documents && property.documents.length > 0) {
      const mapDoc = property.documents.find(doc => doc.type === 'map');
      if (mapDoc) {
        const link = document.createElement('a');
        link.href = mapDoc.url;
        link.download = mapDoc.filename || `${property.name} - Map.pdf`;
        link.target = '_blank';
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
      }
    }
  };

  return (
    <div className="card overflow-hidden group hover:shadow-2xl transition-all duration-500">
      <Link 
        to={`/land-for-sale/${property.id}`}
        className="block"
      >
      {/* Property Image */}
      <div className="relative overflow-hidden">
        <img 
          src={property.image} 
          alt={property.name}
          className="w-full h-64 object-cover group-hover:scale-110 transition-transform duration-700"
        />
        <div className="absolute top-4 left-4">
          <span className="bg-accent-500 text-white px-3 py-1 rounded-full text-sm font-semibold shadow-lg">
            {property.status}
          </span>
        </div>
        <div className="absolute top-4 right-4">
          <span className="bg-white/90 text-gray-800 px-3 py-1 rounded-full text-sm font-semibold shadow-lg">
            View Details
          </span>
        </div>
        
        {/* Overlay on hover */}
        <div className="absolute inset-0 bg-gradient-to-t from-black/60 via-transparent to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-300">
          <div className="absolute bottom-4 left-4 right-4">
            <div className="bg-white/90 backdrop-blur-sm rounded-lg p-3">
              <div className="flex items-center justify-between">
                <span className="text-sm font-medium text-gray-900">Quick View</span>
                <Eye size={16} className="text-primary-500" />
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Property Details */}
      <div className="p-6">
        <div className="flex items-start justify-between mb-4">
          <div className="flex-1">
            <h3 className="text-xl font-bold text-gray-900 mb-2 group-hover:text-primary-500 transition-colors">
              {property.name}
            </h3>
            <div className="flex items-center text-gray-600 mb-3">
              <MapPin size={16} className="mr-2 text-primary-500" />
              <span className="font-medium">{property.location}</span>
            </div>
          </div>
          <div className="text-right">
            <div className="text-2xl font-bold text-primary-500">{property.price}</div>
            <div className="text-sm text-gray-500">per plot</div>
          </div>
        </div>

        <p className="text-gray-600 mb-4 leading-relaxed">
          {property.description.length > 120 
            ? `${property.description.substring(0, 120)}...` 
            : property.description
          }
        </p>

        {/* Property Features */}
        <div className="flex items-center justify-between mb-4">
          <div className="flex items-center text-gray-600">
            <Ruler size={16} className="mr-2 text-primary-500" />
            <span className="text-sm font-medium">{property.size}</span>
          </div>
          <div className="flex items-center text-gray-600">
            <Tag size={16} className="mr-2 text-primary-500" />
            <span className="text-sm font-medium">{property.status}</span>
          </div>
        </div>

        {/* Payment Information */}
        {property.deposit && property.paymentPlan && (
          <div className="mb-4 p-4 bg-primary-50 rounded-lg">
            <div className="grid grid-cols-2 gap-4 text-sm">
              <div className="text-center">
                <div className="text-primary-600 font-semibold">Deposit</div>
                <div className="text-gray-700 font-medium">{property.deposit}</div>
              </div>
              <div className="text-center">
                <div className="text-primary-600 font-semibold">Payment Plan</div>
                <div className="text-gray-700 font-medium">{property.paymentPlan}</div>
              </div>
            </div>
          </div>
        )}

        {/* Features List */}
        <div className="mb-6">
          <div className="flex flex-wrap gap-2">
            {property.features.slice(0, 3).map((feature, index) => (
              <span 
                key={index}
                className="bg-primary-100 text-primary-700 px-3 py-1 rounded-full text-xs font-medium flex items-center"
              >
                <CheckCircle size={12} className="mr-1" />
                {feature}
              </span>
            ))}
            {property.features.length > 3 && (
              <span className="bg-gray-100 text-gray-600 px-3 py-1 rounded-full text-xs font-medium">
                +{property.features.length - 3} more
              </span>
            )}
          </div>
        </div>

        {/* Action Button */}
        <div className="flex justify-center">
          <span className="btn-primary flex items-center justify-center group-hover:scale-105 transition-transform">
            <Eye size={16} className="mr-2" />
            View Details
          </span>
        </div>
      </div>
      </Link>

      {/* Download Map Button */}
      {property.documents && property.documents.find(doc => doc.type === 'map') && (
        <div className="px-6 pb-6">
          <button
            onClick={handleDownloadMap}
            className="w-full bg-secondary-500 hover:bg-secondary-600 text-white font-semibold py-2 px-4 rounded-lg transition-colors duration-300 flex items-center justify-center space-x-2"
          >
            <Download size={16} />
            <span>Download Map</span>
          </button>
        </div>
      )}
    </div>
  );
};

export default PropertyCard;
