import React, { useState } from 'react';
import { Download, FileText, Newspaper, Search, Phone, Mail } from 'lucide-react';
import { properties } from '../data/properties';
import { newsletters } from '../data/newsletters';

const Downloads = () => {
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedCategory, setSelectedCategory] = useState('All');
  const [sortBy, setSortBy] = useState('name');

  // Combine all downloadable content
  const allDownloads = [
    // Property Maps
    ...properties.map(property => ({
      id: `property-${property.id}`,
      name: `${property.name} - Property Map`,
      description: `Property map for ${property.name} located in ${property.location}`,
      category: 'Property Maps',
      type: 'map',
      size: property.documents?.[0]?.size || 'N/A',
      url: property.documents?.[0]?.url,
      filename: property.documents?.[0]?.filename,
      date: 'Available Now'
    })),
    // Newsletters
    ...newsletters.map(newsletter => ({
      id: `newsletter-${newsletter.id}`,
      name: newsletter.title,
      description: newsletter.description,
      category: 'Newsletters',
      type: 'newsletter',
      size: newsletter.documents?.[0]?.size || 'N/A',
      url: newsletter.documents?.[0]?.url,
      filename: newsletter.documents?.[0]?.filename,
      date: newsletter.date
    }))
  ];

  // Get unique categories
  const categories = ['All', ...new Set(allDownloads.map(item => item.category))];

  // Filter and sort downloads
  const filteredDownloads = allDownloads
    .filter(item => {
      const matchesSearch = item.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
                           item.description.toLowerCase().includes(searchTerm.toLowerCase());
      const matchesCategory = selectedCategory === 'All' || item.category === selectedCategory;
      return matchesSearch && matchesCategory;
    })
    .sort((a, b) => {
      if (sortBy === 'name') {
        return a.name.localeCompare(b.name);
      }
      if (sortBy === 'category') {
        return a.category.localeCompare(b.category);
      }
      if (sortBy === 'date') {
        return new Date(b.date) - new Date(a.date);
      }
      return 0;
    });

  const handleDownload = (item) => {
    if (item.url) {
      const link = document.createElement('a');
      link.href = item.url;
      link.download = item.filename || item.name;
      link.target = '_blank';
      link.rel = 'noopener noreferrer';
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    }
  };

  const getIcon = (type) => {
    switch (type) {
      case 'map':
        return <FileText size={20} />;
      case 'newsletter':
        return <Newspaper size={20} />;
      default:
        return <FileText size={20} />;
    }
  };

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Hero Section */}
      <section className="bg-gradient-to-r from-primary-700 to-primary-800 text-white py-20">
        <div className="container-custom text-center">
          <div className="w-20 h-20 bg-white/20 rounded-full flex items-center justify-center mx-auto mb-6">
            <Download size={40} />
          </div>
          <h1 className="text-5xl font-bold mb-6">Downloads</h1>
          <p className="text-xl text-gray-200 max-w-3xl mx-auto">
            Access property maps, newsletters, and other important documents to help you make informed investment decisions.
          </p>
        </div>
      </section>

      {/* Search and Filters */}
      <section className="bg-white py-8 border-b">
        <div className="container-custom">
          <div className="flex flex-col lg:flex-row gap-6 items-center justify-between">
            {/* Search Bar */}
            <div className="flex-1 max-w-2xl">
              <div className="relative">
                <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400" size={20} />
                <input
                  type="text"
                  placeholder="Search documents..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  className="w-full pl-10 pr-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent"
                />
              </div>
            </div>

            {/* Filters */}
            <div className="flex items-center gap-4">
              {/* Category Filter */}
              <select
                value={selectedCategory}
                onChange={(e) => setSelectedCategory(e.target.value)}
                className="px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent"
              >
                {categories.map((category) => (
                  <option key={category} value={category}>
                    {category}
                  </option>
                ))}
              </select>

              {/* Sort Filter */}
              <select
                value={sortBy}
                onChange={(e) => setSortBy(e.target.value)}
                className="px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent"
              >
                <option value="name">Sort by Name</option>
                <option value="category">Sort by Category</option>
                <option value="date">Sort by Date</option>
              </select>
            </div>
          </div>
        </div>
      </section>

      {/* Downloads Grid */}
      <section className="py-16">
        <div className="container-custom">
          {/* Results Summary */}
          <div className="flex items-center justify-between mb-8">
            <h2 className="text-2xl font-bold text-gray-900">Available Documents</h2>
            <div className="text-gray-600">
              Showing <span className="font-semibold">{filteredDownloads.length}</span> of{' '}
              <span className="font-semibold">{allDownloads.length}</span> documents
            </div>
          </div>

          {filteredDownloads.length > 0 ? (
            <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-8">
              {filteredDownloads.map((item) => (
                <div key={item.id} className="bg-white rounded-lg shadow-lg overflow-hidden hover:shadow-xl transition-shadow duration-300">
                  {/* Document Icon */}
                  <div className="bg-primary-50 p-6 text-center">
                    <div className="w-16 h-16 bg-primary-100 rounded-full flex items-center justify-center mx-auto mb-4">
                      {getIcon(item.type)}
                    </div>
                    <span className="inline-block bg-primary-100 text-primary-700 px-3 py-1 rounded-full text-sm font-medium">
                      {item.category}
                    </span>
                  </div>

                  {/* Content */}
                  <div className="p-6">
                    <h3 className="text-xl font-bold text-gray-900 mb-3 line-clamp-2">
                      {item.name}
                    </h3>
                    <p className="text-gray-600 mb-4 line-clamp-3">
                      {item.description}
                    </p>

                    {/* Meta Information */}
                    <div className="flex items-center justify-between text-sm text-gray-500 mb-4">
                      <div className="flex items-center space-x-2">
                        <FileText size={16} />
                        <span>{item.date}</span>
                      </div>
                      <span>{item.size}</span>
                    </div>

                    {/* Download Button */}
                    <button
                      onClick={() => handleDownload(item)}
                      disabled={!item.url}
                      className={`w-full font-semibold py-3 px-4 rounded-lg transition-colors duration-300 flex items-center justify-center space-x-2 ${
                        item.url 
                          ? 'bg-primary-500 hover:bg-primary-600 text-white' 
                          : 'bg-gray-300 text-gray-500 cursor-not-allowed'
                      }`}
                    >
                      <Download size={20} />
                      <span>{item.url ? 'Download' : 'Not Available'}</span>
                    </button>
                  </div>
                </div>
              ))}
            </div>
          ) : (
            <div className="text-center py-16">
              <div className="w-24 h-24 bg-gray-100 rounded-full flex items-center justify-center mx-auto mb-6">
                <Download size={48} className="text-gray-400" />
              </div>
              <h3 className="text-2xl font-bold text-gray-900 mb-4">No Documents Found</h3>
              <p className="text-gray-600 mb-6">
                {searchTerm 
                  ? `No documents match your search for "${searchTerm}". Try adjusting your search terms or filters.`
                  : 'No documents match your current filters. Try adjusting your criteria.'
                }
              </p>
              <button
                onClick={() => {
                  setSearchTerm('');
                  setSelectedCategory('All');
                }}
                className="bg-primary-500 hover:bg-primary-600 text-white font-semibold py-3 px-6 rounded-lg transition-colors duration-300"
              >
                Clear All Filters
              </button>
            </div>
          )}
        </div>
      </section>

      {/* CTA Section */}
      <section className="bg-primary-50 py-16">
        <div className="container-custom text-center">
          <h2 className="text-4xl font-bold text-gray-900 mb-6">Need More Information?</h2>
          <p className="text-xl text-gray-600 mb-8 max-w-2xl mx-auto">
            Can't find what you're looking for? Contact our team for personalized assistance and additional documents.
          </p>
          <div className="flex flex-col sm:flex-row gap-4 justify-center">
            <a 
              href="tel:071102084"
              className="bg-primary-500 hover:bg-primary-600 text-white font-semibold py-3 px-8 rounded-lg transition-colors duration-300 inline-flex items-center justify-center"
            >
              <Phone size={20} className="mr-2" />
              Call Us
            </a>
            <a 
              href="mailto:info@inukaproperties.co.ke"
              className="bg-white hover:bg-gray-50 text-primary-500 font-semibold py-3 px-8 rounded-lg border-2 border-primary-500 transition-colors duration-300 inline-flex items-center justify-center"
            >
              <Mail size={20} className="mr-2" />
              Email Us
            </a>
          </div>
        </div>
      </section>
    </div>
  );
};

export default Downloads;
