import React, { useState } from 'react';
import { useParams, Link } from 'react-router-dom';
import { MapPin, Ruler, Tag, Calendar, CheckCircle, Phone, Mail, ArrowLeft, Star, Building, Shield, DollarSign, Download, Upload } from 'lucide-react';
import { properties } from '../data/properties';
import ReviewsSection from '../components/ReviewsSection';
import BookingForm from '../components/BookingForm';
import DocumentDownload from '../components/DocumentDownload';

const PropertyDetail = () => {
  const { id } = useParams();
  const property = properties.find(p => p.id === parseInt(id));
  const [showBookingForm, setShowBookingForm] = useState(false);

  const handleDownloadMap = () => {
    if (property.documents && property.documents.length > 0) {
      const mapDoc = property.documents.find(doc => doc.type === 'map');
      if (mapDoc) {
        const link = document.createElement('a');
        link.href = mapDoc.url;
        link.download = mapDoc.filename || `${property.name} - Map.pdf`;
        link.target = '_blank';
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
      }
    }
  };

  const handleUploadDocument = () => {
    // This would typically open a file upload modal or redirect to an admin panel
    // For now, we'll show an alert with instructions
    alert('Document upload functionality will be implemented in the admin panel. Please contact the administrator to add documents.');
  };

  if (!property) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <h1 className="text-2xl font-bold text-gray-900 mb-4">Property Not Found</h1>
          <p className="text-gray-600 mb-6">The property you're looking for doesn't exist.</p>
          <Link to="/land-for-sale" className="btn-primary">
            Back to Properties
          </Link>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Hero Section */}
      <section className="bg-gradient-to-r from-primary-700 to-primary-800 text-white py-20">
        <div className="container-custom">
          <div className="flex items-center mb-6">
            <Link to="/land-for-sale" className="flex items-center text-white hover:text-primary-200 transition-colors">
              <ArrowLeft size={20} className="mr-2" />
              Back to Properties
            </Link>
          </div>
          <h1 className="text-4xl font-bold mb-4">{property.name}</h1>
          <div className="flex items-center text-gray-200">
            <MapPin size={20} className="mr-2" />
            <span className="text-lg">{property.location}</span>
          </div>
        </div>
      </section>

      {/* Property Content */}
      <section className="py-16">
        <div className="container-custom">
          <div className="grid lg:grid-cols-3 gap-12">
            {/* Main Content */}
            <div className="lg:col-span-2">
              {/* Property Image */}
              <div className="bg-white rounded-2xl shadow-lg overflow-hidden mb-8">
                <img 
                  src={property.image} 
                  alt={property.name}
                  className="w-full h-96 object-cover"
                />
                <div className="p-6">
                  <div className="flex items-center justify-between mb-4">
                    <span className="bg-accent-500 text-white px-4 py-2 rounded-full text-sm font-semibold">
                      {property.status}
                    </span>
                                         <div className="text-3xl font-bold text-secondary-500">{property.price}</div>
                  </div>
                  
                  {/* Property Features */}
                  <div className="grid grid-cols-2 gap-4 mb-6">
                    <div className="flex items-center text-gray-600">
                      <Ruler size={20} className="mr-3 text-primary-500" />
                      <span className="font-medium">{property.size}</span>
                    </div>
                    <div className="flex items-center text-gray-600">
                      <Tag size={20} className="mr-3 text-primary-500" />
                      <span className="font-medium">{property.status}</span>
                    </div>
                  </div>

                  {/* Payment Information */}
                  {property.deposit && property.paymentPlan && (
                    <div className="bg-primary-50 rounded-xl p-6 mb-6">
                      <h3 className="text-xl font-bold text-gray-900 mb-4">Payment Details</h3>
                      <div className="grid grid-cols-2 gap-6">
                        <div className="text-center">
                          <div className="text-primary-600 font-semibold mb-2">Deposit</div>
                          <div className="text-2xl font-bold text-gray-900">{property.deposit}</div>
                        </div>
                        <div className="text-center">
                          <div className="text-primary-600 font-semibold mb-2">Payment Plan</div>
                          <div className="text-lg text-gray-700">{property.paymentPlan}</div>
                        </div>
                      </div>
                    </div>
                  )}

                  {/* Features List */}
                  <div className="mb-6">
                    <h3 className="text-xl font-bold text-gray-900 mb-4">Property Features</h3>
                    <div className="grid grid-cols-2 gap-3">
                      {property.features.map((feature, index) => (
                        <div key={index} className="flex items-center text-gray-700">
                          <CheckCircle size={16} className="mr-2 text-primary-500" />
                          <span>{feature}</span>
                        </div>
                      ))}
                    </div>
                  </div>

                  {/* Description */}
                  <div className="mb-8">
                    <h3 className="text-xl font-bold text-gray-900 mb-4">Description</h3>
                    <p className="text-gray-700 leading-relaxed">{property.description}</p>
                  </div>

                                     {/* Virtual Tour */}
                   {property.virtualTour && (
                     <div className="mb-8">
                       <h3 className="text-xl font-bold text-gray-900 mb-4">Virtual Tour</h3>
                       <div className="aspect-video bg-gray-100 rounded-xl overflow-hidden">
                         <iframe
                           src={property.virtualTour.replace('youtu.be/', 'youtube.com/embed/').replace('youtube.com/watch?v=', 'youtube.com/embed/')}
                           title={`Virtual Tour - ${property.name}`}
                           className="w-full h-full"
                           frameBorder="0"
                           allowFullScreen
                           allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                         />
                       </div>
                     </div>
                   )}

                   {/* Documents Section */}
                   {property.documents && property.documents.length > 0 && (
                     <div className="mb-8">
                       <DocumentDownload 
                         documents={property.documents}
                         title="Property Documents"
                         type="property"
                       />
                     </div>
                   )}
                </div>
              </div>
            </div>

            {/* Sidebar */}
            <div className="lg:col-span-1">
              {/* Quick Actions */}
              <div className="bg-white rounded-2xl shadow-lg p-6 mb-6">
                <h3 className="text-xl font-bold text-gray-900 mb-4">Quick Actions</h3>
                <div className="space-y-3">
                                     <button 
                     onClick={() => setShowBookingForm(true)}
                     className="w-full btn-primary flex items-center justify-center"
                   >
                     <Calendar size={20} className="mr-2" />
                     Book Viewing
                   </button>
                                     <a 
                     href="tel:071102084"
                     className="w-full btn-outline flex items-center justify-center transition-all duration-300 transform hover:scale-105"
                   >
                     <Phone size={20} className="mr-2" />
                     Call Now
                   </a>
                   <a 
                     href={`mailto:info@inukaproperties.co.ke?subject=Inquiry about ${property.name}&body=Hello, I'm interested in ${property.name} located in ${property.location}. Please provide more information about this property.`}
                     className="w-full btn-outline-secondary flex items-center justify-center transition-all duration-300 transform hover:scale-105"
                   >
                     <Mail size={20} className="mr-2" />
                     Send Inquiry
                   </a>
                                       {property.documents && property.documents.find(doc => doc.type === 'map') && (
                      <button 
                        onClick={handleDownloadMap}
                        className="w-full btn-outline flex items-center justify-center transition-all duration-300 transform hover:scale-105"
                      >
                        <Download size={20} className="mr-2" />
                        Download Map
                      </button>
                    )}
                    <button 
                      onClick={handleUploadDocument}
                      className="w-full btn-outline-secondary flex items-center justify-center transition-all duration-300 transform hover:scale-105"
                    >
                      <Upload size={20} className="mr-2" />
                      Add Document
                    </button>
                </div>
              </div>

              {/* Contact Information */}
              <div className="bg-white rounded-2xl shadow-lg p-6 mb-6">
                <h3 className="text-xl font-bold text-gray-900 mb-4">Contact Information</h3>
                <div className="space-y-4">
                  <div className="flex items-center text-gray-700">
                    <Phone size={20} className="mr-3 text-primary-500" />
                    <a href="tel:071102084" className="hover:text-primary-600 transition-colors">
                      0711 02084
                    </a>
                  </div>
                  <div className="flex items-center text-gray-700">
                    <Mail size={20} className="mr-3 text-primary-500" />
                    <a href="mailto:info@inukaproperties.co.ke" className="hover:text-primary-600 transition-colors">
                      info@inukaproperties.co.ke
                    </a>
                  </div>
                  <div className="flex items-center text-gray-700">
                    <Building size={20} className="mr-3 text-primary-500" />
                    <span>Nyali, Mombasa</span>
                  </div>
                </div>
              </div>

              {/* Why Choose Us */}
              <div className="bg-white rounded-2xl shadow-lg p-6">
                <h3 className="text-xl font-bold text-gray-900 mb-4">Why Choose Inuka Properties?</h3>
                <div className="space-y-4">
                  <div className="flex items-start">
                    <Shield size={20} className="mr-3 text-primary-500 mt-1 flex-shrink-0" />
                    <div>
                      <h4 className="font-semibold text-gray-900">Genuine Title Deeds</h4>
                      <p className="text-sm text-gray-600">All properties come with verified documentation</p>
                    </div>
                  </div>
                  <div className="flex items-start">
                    <Star size={20} className="mr-3 text-primary-500 mt-1 flex-shrink-0" />
                    <div>
                      <h4 className="font-semibold text-gray-900">Prime Locations</h4>
                      <p className="text-sm text-gray-600">Strategic locations with high growth potential</p>
                    </div>
                  </div>
                  <div className="flex items-start">
                    <DollarSign size={20} className="mr-3 text-primary-500 mt-1 flex-shrink-0" />
                    <div>
                      <h4 className="font-semibold text-gray-900">Flexible Payment Plans</h4>
                      <p className="text-sm text-gray-600">Easy installment options available</p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* Reviews Section */}
      <section className="py-16">
        <div className="container-custom">
          <ReviewsSection 
            propertyId={property.id}
            propertyName={property.name}
            propertyLocation={property.location}
          />
        </div>
      </section>

      {/* Related Properties */}
      <section className="py-16 bg-gray-100">
        <div className="container-custom">
          <h2 className="text-3xl font-bold text-gray-900 mb-8 text-center">Similar Properties</h2>
          <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-8">
            {properties
              .filter(p => p.id !== property.id && p.location === property.location)
              .slice(0, 3)
              .map((relatedProperty) => (
                <Link 
                  key={relatedProperty.id} 
                  to={`/land-for-sale/${relatedProperty.id}`}
                  className="bg-white rounded-xl shadow-lg overflow-hidden hover:shadow-xl transition-shadow"
                >
                  <img 
                    src={relatedProperty.image} 
                    alt={relatedProperty.name}
                    className="w-full h-48 object-cover"
                  />
                  <div className="p-4">
                    <h3 className="font-bold text-gray-900 mb-2">{relatedProperty.name}</h3>
                                         <p className="text-secondary-500 font-semibold mb-2">{relatedProperty.price}</p>
                    <div className="flex items-center text-gray-600 text-sm">
                      <MapPin size={16} className="mr-1" />
                      {relatedProperty.location}
                    </div>
                  </div>
                </Link>
              ))}
          </div>
        </div>
             </section>

       {/* Booking Form Modal */}
       <BookingForm
         isOpen={showBookingForm}
         onClose={() => setShowBookingForm(false)}
         propertyName={property.name}
         propertyLocation={property.location}
       />
     </div>
   );
 };

export default PropertyDetail;
