import React, { useState, useEffect } from 'react';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import { Menu, X, ChevronDown, Phone, Mail, MapPin, Search, Clock } from 'lucide-react';

const Header = () => {
  const [isOpen, setIsOpen] = useState(false);
  const [isScrolled, setIsScrolled] = useState(false);
  const [activeDropdown, setActiveDropdown] = useState(null);
  const [searchQuery, setSearchQuery] = useState('');
  const location = useLocation();
  const navigate = useNavigate();

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 50);
    };
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  // Close mobile menu when route changes
  useEffect(() => {
    setIsOpen(false);
    setActiveDropdown(null);
  }, [location.pathname]);

  const toggleDropdown = (dropdown) => {
    setActiveDropdown(activeDropdown === dropdown ? null : dropdown);
  };

  const closeMobileMenu = () => {
    setIsOpen(false);
    setActiveDropdown(null);
  };

  const handleSearch = (e) => {
    e.preventDefault();
    if (searchQuery.trim()) {
      navigate(`/land-for-sale?search=${encodeURIComponent(searchQuery.trim())}`);
      setSearchQuery('');
    }
  };

  const isActive = (path) => location.pathname === path;

  return (
    <>
      {/* Top Bar */}
      <div className="bg-primary-700 text-white py-2 sm:py-3 sticky top-0 z-40">
        <div className="container-custom">
          <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between gap-2 sm:gap-4">
            {/* Office Info */}
            <div className="flex flex-col sm:flex-row items-start sm:items-center gap-2 sm:gap-4 text-xs sm:text-sm">
              <div className="flex items-center space-x-1 sm:space-x-2">
                <Phone size={14} className="text-primary-300 flex-shrink-0" />
                <span className="font-medium truncate">0711 02084</span>
              </div>
              <div className="flex items-center space-x-1 sm:space-x-2">
                <Mail size={14} className="text-primary-300 flex-shrink-0" />
                <span className="font-medium truncate hidden sm:inline">info@inukaproperties.co.ke</span>
                <span className="font-medium truncate sm:hidden">info@inuka...</span>
              </div>
              <div className="flex items-center space-x-1 sm:space-x-2">
                <MapPin size={14} className="text-primary-300 flex-shrink-0" />
                <span className="font-medium truncate hidden md:inline">Nyali, Links Road, Opposite Kigothos Hotel</span>
                <span className="font-medium truncate md:hidden">Nyali, Links Road</span>
              </div>
              <div className="flex items-center space-x-1 sm:space-x-2">
                <Clock size={14} className="text-primary-300 flex-shrink-0" />
                <span className="font-medium truncate hidden lg:inline">Mon-Fri: 8AM-5PM | Sat: 9AM-1PM</span>
                <span className="font-medium truncate lg:hidden">Mon-Fri: 8AM-5PM</span>
              </div>
            </div>

            {/* Search Bar */}
            <div className="flex items-center space-x-2">
              <form onSubmit={handleSearch} className="relative w-full sm:w-auto">
                <input
                  type="text"
                  placeholder="Search properties..."
                  value={searchQuery}
                  onChange={(e) => setSearchQuery(e.target.value)}
                  className="w-full sm:w-64 px-3 sm:px-4 py-1.5 sm:py-2 pl-8 sm:pl-10 pr-16 sm:pr-20 text-gray-800 bg-white rounded-lg focus:outline-none focus:ring-2 focus:ring-primary-300 focus:ring-opacity-50 text-xs sm:text-sm"
                  aria-label="Search properties by location"
                />
                <Search size={14} className="absolute left-2 sm:left-3 top-1/2 transform -translate-y-1/2 text-gray-400" />
                <button
                  type="submit"
                  className="absolute right-1 sm:right-2 top-1/2 transform -translate-y-1/2 bg-primary-500 hover:bg-primary-600 text-white px-2 sm:px-3 py-0.5 sm:py-1 rounded-md text-xs sm:text-sm font-medium transition-colors"
                  aria-label="Search"
                >
                  Search
                </button>
              </form>
            </div>
          </div>
        </div>
      </div>

      {/* Main Header */}
      <header className={`fixed top-0 left-0 right-0 z-50 transition-all duration-300 ${
        isScrolled ? 'bg-white shadow-lg' : 'bg-white/95 backdrop-blur-sm'
      }`} style={{ top: '72px' }}>
        <div className="container-custom">
          <div className="flex items-center justify-between py-3 sm:py-4">
            {/* Logo */}
            <Link to="/" className="flex items-center space-x-2 sm:space-x-3" aria-label="Inuka Properties Home">
              <div className="w-12 h-12 sm:w-16 sm:h-16 rounded-lg overflow-hidden shadow-lg flex-shrink-0">
                <img 
                  src="https://res.cloudinary.com/dyfnobo9r/image/upload/v1756029907/download_2_pwhdnm.png"
                  alt="Inuka Properties Logo"
                  className="w-full h-full object-cover"
                  loading="eager"
                />
              </div>
            </Link>

            {/* Desktop Navigation */}
            <nav className="hidden lg:flex items-center space-x-6 xl:space-x-8" role="navigation" aria-label="Main navigation">
              <Link 
                to="/" 
                className={`font-medium transition-colors ${
                  isActive('/') ? 'text-primary-500' : 'text-gray-700 hover:text-primary-500'
                }`}
                aria-current={isActive('/') ? 'page' : undefined}
              >
                Home
              </Link>
              
              <Link 
                to="/land-for-sale" 
                className={`font-medium transition-colors ${
                  isActive('/land-for-sale') ? 'text-primary-500' : 'text-gray-700 hover:text-primary-500'
                }`}
                aria-current={isActive('/land-for-sale') ? 'page' : undefined}
              >
                Land for Sale
              </Link>

              {/* About Us Dropdown */}
              <div className="relative">
                <button
                  onClick={() => toggleDropdown('about')}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter' || e.key === ' ') {
                      e.preventDefault();
                      toggleDropdown('about');
                    }
                  }}
                  className={`font-medium transition-colors flex items-center space-x-1 ${
                    location.pathname.startsWith('/about-us') ? 'text-primary-500' : 'text-gray-700 hover:text-primary-500'
                  }`}
                  aria-expanded={activeDropdown === 'about'}
                  aria-haspopup="true"
                  aria-label="About Us menu"
                >
                  <span>About Us</span>
                  <ChevronDown size={16} className={`transition-transform ${activeDropdown === 'about' ? 'rotate-180' : ''}`} />
                </button>
                
                {activeDropdown === 'about' && (
                  <div className="absolute top-full left-0 mt-2 w-48 bg-white rounded-lg shadow-xl border border-gray-100 py-2" role="menu">
                    <Link to="/about-us/who-we-are" className="block px-4 py-2 text-gray-700 hover:bg-primary-50 hover:text-primary-500" role="menuitem" onClick={closeMobileMenu}>
                      Who We Are
                    </Link>
                    <Link to="/about-us/why-us" className="block px-4 py-2 text-gray-700 hover:bg-primary-50 hover:text-primary-500" role="menuitem" onClick={closeMobileMenu}>
                      Why Us
                    </Link>
                    <Link to="/about-us/our-team" className="block px-4 py-2 text-gray-700 hover:bg-primary-50 hover:text-primary-500" role="menuitem" onClick={closeMobileMenu}>
                      Our Team
                    </Link>
                    <Link to="/about-us/csr" className="block px-4 py-2 text-gray-700 hover:bg-primary-50 hover:text-primary-500" role="menuitem" onClick={closeMobileMenu}>
                      CSR
                    </Link>
                    <Link to="/about-us/our-partners" className="block px-4 py-2 text-gray-700 hover:bg-primary-50 hover:text-primary-500" role="menuitem" onClick={closeMobileMenu}>
                      Our Partners
                    </Link>
                  </div>
                )}
              </div>

              {/* IAPL Insider Dropdown */}
              <div className="relative">
                <button
                  onClick={() => toggleDropdown('insider')}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter' || e.key === ' ') {
                      e.preventDefault();
                      toggleDropdown('insider');
                    }
                  }}
                  className={`font-medium transition-colors flex items-center space-x-1 ${
                    location.pathname.startsWith('/iapl-insider') ? 'text-primary-500' : 'text-gray-700 hover:text-primary-500'
                  }`}
                  aria-expanded={activeDropdown === 'insider'}
                  aria-haspopup="true"
                  aria-label="IAPL Insider menu"
                >
                  <span>IAPL INSIDER</span>
                  <ChevronDown size={16} className={`transition-transform ${activeDropdown === 'insider' ? 'rotate-180' : ''}`} />
                </button>
                
                {activeDropdown === 'insider' && (
                  <div className="absolute top-full left-0 mt-2 w-48 bg-white rounded-lg shadow-xl border border-gray-100 py-2" role="menu">
                    <Link to="/iapl-insider/blogs" className="block px-4 py-2 text-gray-700 hover:bg-primary-50 hover:text-primary-500" role="menuitem" onClick={closeMobileMenu}>
                      Blogs
                    </Link>
                    <Link to="/iapl-insider/news" className="block px-4 py-2 text-gray-700 hover:bg-primary-50 hover:text-primary-500" role="menuitem" onClick={closeMobileMenu}>
                      News
                    </Link>
                    <Link to="/iapl-insider/market-research" className="block px-4 py-2 text-gray-700 hover:bg-primary-50 hover:text-primary-500" role="menuitem" onClick={closeMobileMenu}>
                      Market Research
                    </Link>
                  </div>
                )}
              </div>

              {/* Testimonials & Downloads Dropdown */}
              <div className="relative">
                <button
                  onClick={() => toggleDropdown('testimonials')}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter' || e.key === ' ') {
                      e.preventDefault();
                      toggleDropdown('testimonials');
                    }
                  }}
                  className={`font-medium transition-colors flex items-center space-x-1 ${
                    location.pathname.startsWith('/testimonials') || location.pathname.startsWith('/downloads') ? 'text-primary-500' : 'text-gray-700 hover:text-primary-500'
                  }`}
                  aria-expanded={activeDropdown === 'testimonials'}
                  aria-haspopup="true"
                  aria-label="Testimonials and Downloads menu"
                >
                  <span>Testimonials</span>
                  <ChevronDown size={16} className={`transition-transform ${activeDropdown === 'testimonials' ? 'rotate-180' : ''}`} />
                </button>
                
                {activeDropdown === 'testimonials' && (
                  <div className="absolute top-full left-0 mt-2 w-48 bg-white rounded-lg shadow-xl border border-gray-100 py-2" role="menu">
                    <Link to="/testimonials" className="block px-4 py-2 text-gray-700 hover:bg-primary-50 hover:text-primary-500" role="menuitem" onClick={closeMobileMenu}>
                      Client Testimonials
                    </Link>
                    <Link to="/downloads" className="block px-4 py-2 text-gray-700 hover:bg-primary-50 hover:text-primary-500" role="menuitem" onClick={closeMobileMenu}>
                      Downloads
                    </Link>
                  </div>
                )}
              </div>
              
              <Link 
                to="/contact-us" 
                className={`font-medium transition-colors ${
                  isActive('/contact-us') ? 'text-primary-500' : 'text-gray-700 hover:text-primary-500'
                }`}
                aria-current={isActive('/contact-us') ? 'page' : undefined}
              >
                Contact Us
              </Link>
            </nav>

            {/* CTA Button */}
            <div className="hidden lg:block">
              <Link to="/contact-us" className="btn-primary text-sm sm:text-base px-4 sm:px-6 py-2 sm:py-3">
                Book a Site Visit
              </Link>
            </div>

            {/* Mobile Menu Button */}
            <button
              onClick={() => setIsOpen(!isOpen)}
              className="lg:hidden p-2 rounded-lg hover:bg-gray-100 focus:outline-none focus:ring-2 focus:ring-primary-500"
              aria-label={isOpen ? 'Close menu' : 'Open menu'}
              aria-expanded={isOpen}
            >
              {isOpen ? <X size={24} /> : <Menu size={24} />}
            </button>
          </div>
        </div>

        {/* Mobile Navigation */}
        {isOpen && (
          <div className="lg:hidden bg-white border-t border-gray-100 shadow-lg">
            <div className="container-custom py-4">
              <nav className="space-y-2" role="navigation" aria-label="Mobile navigation">
                <Link to="/" className="block py-2 px-4 rounded-lg hover:bg-primary-50 focus:bg-primary-50 focus:outline-none" onClick={closeMobileMenu} aria-current={isActive('/') ? 'page' : undefined}>
                  Home
                </Link>
                <Link to="/land-for-sale" className="block py-2 px-4 rounded-lg hover:bg-primary-50 focus:bg-primary-50 focus:outline-none" onClick={closeMobileMenu} aria-current={isActive('/land-for-sale') ? 'page' : undefined}>
                  Land for Sale
                </Link>
                
                {/* About Us Mobile */}
                <div>
                  <button
                    onClick={() => toggleDropdown('about-mobile')}
                    onKeyDown={(e) => {
                      if (e.key === 'Enter' || e.key === ' ') {
                        e.preventDefault();
                        toggleDropdown('about-mobile');
                      }
                    }}
                    className="w-full text-left py-2 px-4 rounded-lg hover:bg-primary-50 focus:bg-primary-50 focus:outline-none flex items-center justify-between"
                    aria-expanded={activeDropdown === 'about-mobile'}
                    aria-haspopup="true"
                  >
                    <span>About Us</span>
                    <ChevronDown size={16} className={`transition-transform ${activeDropdown === 'about-mobile' ? 'rotate-180' : ''}`} />
                  </button>
                  
                  {activeDropdown === 'about-mobile' && (
                    <div className="ml-4 mt-2 space-y-1" role="menu">
                      <Link to="/about-us/who-we-are" className="block py-2 px-4 rounded-lg hover:bg-primary-50 focus:bg-primary-50 focus:outline-none" onClick={closeMobileMenu} role="menuitem">
                        Who We Are
                      </Link>
                      <Link to="/about-us/why-us" className="block py-2 px-4 rounded-lg hover:bg-primary-50 focus:bg-primary-50 focus:outline-none" onClick={closeMobileMenu} role="menuitem">
                        Why Us
                      </Link>
                      <Link to="/about-us/our-team" className="block py-2 px-4 rounded-lg hover:bg-primary-50 focus:bg-primary-50 focus:outline-none" onClick={closeMobileMenu} role="menuitem">
                        Our Team
                      </Link>
                      <Link to="/about-us/csr" className="block py-2 px-4 rounded-lg hover:bg-primary-50 focus:bg-primary-50 focus:outline-none" onClick={closeMobileMenu} role="menuitem">
                        CSR
                      </Link>
                      <Link to="/about-us/our-partners" className="block py-2 px-4 rounded-lg hover:bg-primary-50 focus:bg-primary-50 focus:outline-none" onClick={closeMobileMenu} role="menuitem">
                        Our Partners
                      </Link>
                    </div>
                  )}
                </div>

                {/* IAPL Insider Mobile */}
                <div>
                  <button
                    onClick={() => toggleDropdown('insider-mobile')}
                    onKeyDown={(e) => {
                      if (e.key === 'Enter' || e.key === ' ') {
                        e.preventDefault();
                        toggleDropdown('insider-mobile');
                      }
                    }}
                    className="w-full text-left py-2 px-4 rounded-lg hover:bg-primary-50 focus:bg-primary-50 focus:outline-none flex items-center justify-between"
                    aria-expanded={activeDropdown === 'insider-mobile'}
                    aria-haspopup="true"
                  >
                    <span>IAPL INSIDER</span>
                    <ChevronDown size={16} className={`transition-transform ${activeDropdown === 'insider-mobile' ? 'rotate-180' : ''}`} />
                  </button>
                  
                  {activeDropdown === 'insider-mobile' && (
                    <div className="ml-4 mt-2 space-y-1" role="menu">
                      <Link to="/iapl-insider/blogs" className="block py-2 px-4 rounded-lg hover:bg-primary-50 focus:bg-primary-50 focus:outline-none" onClick={closeMobileMenu} role="menuitem">
                        Blogs
                      </Link>
                      <Link to="/iapl-insider/news" className="block py-2 px-4 rounded-lg hover:bg-primary-50 focus:bg-primary-50 focus:outline-none" onClick={closeMobileMenu} role="menuitem">
                        News
                      </Link>
                      <Link to="/iapl-insider/market-research" className="block py-2 px-4 rounded-lg hover:bg-primary-50 focus:bg-primary-50 focus:outline-none" onClick={closeMobileMenu} role="menuitem">
                        Market Research
                      </Link>
                    </div>
                  )}
                </div>

                {/* Testimonials & Downloads Mobile */}
                <div>
                  <button
                    onClick={() => toggleDropdown('testimonials-mobile')}
                    onKeyDown={(e) => {
                      if (e.key === 'Enter' || e.key === ' ') {
                        e.preventDefault();
                        toggleDropdown('testimonials-mobile');
                      }
                    }}
                    className="w-full text-left py-2 px-4 rounded-lg hover:bg-primary-50 focus:bg-primary-50 focus:outline-none flex items-center justify-between"
                    aria-expanded={activeDropdown === 'testimonials-mobile'}
                    aria-haspopup="true"
                  >
                    <span>Testimonials</span>
                    <ChevronDown size={16} className={`transition-transform ${activeDropdown === 'testimonials-mobile' ? 'rotate-180' : ''}`} />
                  </button>
                  
                  {activeDropdown === 'testimonials-mobile' && (
                    <div className="ml-4 mt-2 space-y-1" role="menu">
                      <Link to="/testimonials" className="block py-2 px-4 rounded-lg hover:bg-primary-50 focus:bg-primary-50 focus:outline-none" onClick={closeMobileMenu} role="menuitem">
                        Client Testimonials
                      </Link>
                      <Link to="/downloads" className="block py-2 px-4 rounded-lg hover:bg-primary-50 focus:bg-primary-50 focus:outline-none" onClick={closeMobileMenu} role="menuitem">
                        Downloads
                      </Link>
                    </div>
                  )}
                </div>
                <Link to="/contact-us" className="block py-2 px-4 rounded-lg hover:bg-primary-50 focus:bg-primary-50 focus:outline-none" onClick={closeMobileMenu} aria-current={isActive('/contact-us') ? 'page' : undefined}>
                  Contact Us
                </Link>
                
                <div className="pt-4 border-t border-gray-200">
                  <Link to="/contact-us" className="btn-primary w-full text-center text-sm sm:text-base px-4 py-2 sm:py-3" onClick={closeMobileMenu}>
                    Book a Site Visit
                  </Link>
                </div>
              </nav>
            </div>
          </div>
        )}
      </header>

      {/* Spacer for fixed header */}
      <div className="h-40"></div>
    </>
  );
};

export default Header;
