import React from 'react';
import { Settings } from 'lucide-react';

const CookiePolicy = () => {
  return (
    <div className="min-h-screen bg-gray-50">
      {/* Hero Section */}
      <section className="bg-gradient-to-r from-primary-700 to-primary-800 text-white py-20">
        <div className="container-custom text-center">
          <div className="w-20 h-20 bg-white/20 rounded-full flex items-center justify-center mx-auto mb-6">
            <Settings size={40} />
          </div>
          <h1 className="text-5xl font-bold mb-6">Cookie Policy</h1>
          <p className="text-xl text-gray-200 max-w-3xl mx-auto">
            This Cookie Policy explains how Inuka Afrika Properties uses cookies and similar technologies on our website.
          </p>
        </div>
      </section>

      {/* Content Section */}
      <section className="py-16">
        <div className="container-custom">
          <div className="max-w-4xl mx-auto">
            {/* Introduction */}
            <div className="bg-white rounded-lg shadow-lg p-8 mb-8">
              <p className="text-lg text-gray-700 leading-relaxed mb-6">
                This Cookie Policy explains how Inuka Afrika Properties Limited ("we", "us", "our") uses cookies and similar technologies when you visit our website. This policy should be read alongside our Privacy Policy, which explains how we use your personal data.
              </p>
            </div>

            {/* Policy Sections */}
            <div className="space-y-8">
              {/* What are cookies */}
              <div className="bg-white rounded-lg shadow-lg p-8">
                <h2 className="text-3xl font-bold text-gray-900 mb-6">What are cookies?</h2>
                <p className="text-gray-700 mb-4">
                  Cookies are small text files that are stored on your device (computer, tablet, or mobile) when you visit a website. They help the website remember information about your visit, such as your preferred language and other settings, which can make your next visit easier and the site more useful to you.
                </p>
                <p className="text-gray-700">
                  We also use similar technologies such as web beacons, pixel tags, and local storage to collect information about your browsing activities.
                </p>
              </div>

              {/* Types of cookies we use */}
              <div className="bg-white rounded-lg shadow-lg p-8">
                <h2 className="text-3xl font-bold text-gray-900 mb-6">Types of cookies we use</h2>
                
                <div className="space-y-6">
                  <div>
                    <h3 className="text-xl font-semibold text-gray-900 mb-3">Essential Cookies</h3>
                    <p className="text-gray-700 mb-2">
                      These cookies are necessary for the website to function properly. They enable basic functions like page navigation, access to secure areas, and form submissions. The website cannot function properly without these cookies.
                    </p>
                    <ul className="list-disc list-inside text-gray-700 space-y-1">
                      <li>Session management and security</li>
                      <li>Form submission and validation</li>
                      <li>Language preferences</li>
                    </ul>
                  </div>

                  <div>
                    <h3 className="text-xl font-semibold text-gray-900 mb-3">Analytics Cookies</h3>
                    <p className="text-gray-700 mb-2">
                      These cookies help us understand how visitors interact with our website by collecting and reporting information anonymously.
                    </p>
                    <ul className="list-disc list-inside text-gray-700 space-y-1">
                      <li>Page views and navigation patterns</li>
                      <li>Time spent on pages</li>
                      <li>Error tracking and performance monitoring</li>
                    </ul>
                  </div>

                  <div>
                    <h3 className="text-xl font-semibold text-gray-900 mb-3">Functional Cookies</h3>
                    <p className="text-gray-700 mb-2">
                      These cookies enable enhanced functionality and personalization, such as remembering your preferences and settings.
                    </p>
                    <ul className="list-disc list-inside text-gray-700 space-y-1">
                      <li>User preferences and settings</li>
                      <li>Chat bot interactions</li>
                      <li>Property search history</li>
                    </ul>
                  </div>

                  <div>
                    <h3 className="text-xl font-semibold text-gray-900 mb-3">Marketing Cookies</h3>
                    <p className="text-gray-700 mb-2">
                      These cookies are used to track visitors across websites to display relevant and engaging advertisements.
                    </p>
                    <ul className="list-disc list-inside text-gray-700 space-y-1">
                      <li>Ad targeting and retargeting</li>
                      <li>Campaign effectiveness measurement</li>
                      <li>Social media integration</li>
                    </ul>
                  </div>
                </div>
              </div>

              {/* Third-party cookies */}
              <div className="bg-white rounded-lg shadow-lg p-8">
                <h2 className="text-3xl font-bold text-gray-900 mb-6">Third-party cookies</h2>
                <p className="text-gray-700 mb-4">
                  We may use third-party services that place cookies on your device. These services include:
                </p>
                <ul className="list-disc list-inside space-y-2 text-gray-700 mb-4">
                  <li><strong>Google Analytics:</strong> To analyze website traffic and user behavior</li>
                  <li><strong>EmailJS:</strong> For contact form functionality and email notifications</li>
                  <li><strong>Cloudinary:</strong> For image hosting and optimization</li>
                  <li><strong>Google Maps:</strong> For location services and maps</li>
                  <li><strong>Social Media Platforms:</strong> For social media integration and sharing</li>
                </ul>
                <p className="text-gray-700">
                  These third-party services have their own privacy policies and cookie policies. We encourage you to review their policies for more information.
                </p>
              </div>

              {/* Managing cookies */}
              <div className="bg-white rounded-lg shadow-lg p-8">
                <h2 className="text-3xl font-bold text-gray-900 mb-6">Managing cookies</h2>
                
                <div className="space-y-6">
                  <div>
                    <h3 className="text-xl font-semibold text-gray-900 mb-3">Browser Settings</h3>
                    <p className="text-gray-700 mb-4">
                      Most web browsers allow you to control cookies through their settings preferences. You can:
                    </p>
                    <ul className="list-disc list-inside space-y-2 text-gray-700">
                      <li>Delete existing cookies</li>
                      <li>Block cookies from being set</li>
                      <li>Set your browser to notify you when cookies are being set</li>
                      <li>Choose which cookies to accept or reject</li>
                    </ul>
                  </div>

                  <div>
                    <h3 className="text-xl font-semibold text-gray-900 mb-3">Cookie Consent</h3>
                    <p className="text-gray-700 mb-4">
                      When you first visit our website, you will see a cookie consent banner. You can:
                    </p>
                    <ul className="list-disc list-inside space-y-2 text-gray-700">
                      <li>Accept all cookies</li>
                      <li>Customize your cookie preferences</li>
                      <li>Reject non-essential cookies</li>
                    </ul>
                  </div>

                  <div>
                    <h3 className="text-xl font-semibold text-gray-900 mb-3">Updating Preferences</h3>
                    <p className="text-gray-700">
                      You can change your cookie preferences at any time by clicking on the "Cookie Settings" link in our footer or by contacting us directly.
                    </p>
                  </div>
                </div>
              </div>

              {/* Impact of disabling cookies */}
              <div className="bg-white rounded-lg shadow-lg p-8">
                <h2 className="text-3xl font-bold text-gray-900 mb-6">Impact of disabling cookies</h2>
                <p className="text-gray-700 mb-4">
                  If you choose to disable cookies, some features of our website may not function properly:
                </p>
                <ul className="list-disc list-inside space-y-2 text-gray-700">
                  <li>You may need to re-enter information each time you visit</li>
                  <li>Some website features may not work as expected</li>
                  <li>Personalized content and recommendations may not be available</li>
                  <li>Analytics data may be less accurate</li>
                </ul>
              </div>

              {/* Updates to this policy */}
              <div className="bg-white rounded-lg shadow-lg p-8">
                <h2 className="text-3xl font-bold text-gray-900 mb-6">Updates to this policy</h2>
                <p className="text-gray-700">
                  We may update this Cookie Policy from time to time to reflect changes in our practices or for other operational, legal, or regulatory reasons. We will notify you of any material changes by posting the updated policy on our website with a new effective date.
                </p>
              </div>

              {/* Contact information */}
              <div className="bg-white rounded-lg shadow-lg p-8">
                <h2 className="text-3xl font-bold text-gray-900 mb-6">Contact us</h2>
                <p className="text-gray-700 mb-4">
                  If you have any questions about our use of cookies or this Cookie Policy, please contact us:
                </p>
                <div className="bg-primary-50 rounded-lg p-6">
                  <div className="space-y-3">
                    <div>
                      <strong>Email:</strong> privacy@inukaproperties.co.ke
                    </div>
                    <div>
                      <strong>Phone:</strong> 0711 082084
                    </div>
                    <div>
                      <strong>Address:</strong> Along Links Road Opposite Kigothos Hotel and Apartment, Nyali, Mombasa, Kenya
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
};

export default CookiePolicy;
