import React, { useState, useEffect } from 'react';
import { Star, MessageCircle, ThumbsUp, User, Calendar } from 'lucide-react';
import { API_CONFIG } from '../config/api';

const ReviewsSection = ({ propertyId, propertyName, propertyLocation }) => {
  const [reviews, setReviews] = useState([]);
  const [loading, setLoading] = useState(true);
  const [showReviewForm, setShowReviewForm] = useState(false);
  const [newReview, setNewReview] = useState({
    name: '',
    rating: 5,
    comment: '',
    propertyExperience: 'Excellent'
  });

  // Test Google Places API connection
  const testGooglePlacesAPI = async () => {
    try {
      const testQuery = `Inuka Afrika Properties ${propertyLocation}`;
      const url = `https://maps.googleapis.com/maps/api/place/textsearch/json?query=${encodeURIComponent(testQuery)}&key=${API_CONFIG.GOOGLE_PLACES_API_KEY}`;
      
      const response = await fetch(url);
      const data = await response.json();
      
      console.log('Google Places API Test Result:', data);
      return data.status === 'OK';
    } catch (error) {
      console.error('Google Places API Test Error:', error);
      return false;
    }
  };

  // Mock Google Reviews data (replace with actual API calls)
  const mockGoogleReviews = [
    {
      id: 1,
      author: 'John Mwangi',
      rating: 5,
      comment: 'Excellent service! The plot I bought in Chumani Phase 3 is exactly as described. Great location and value for money.',
      date: '2024-01-15',
      source: 'Google',
      verified: true,
      helpful: 12
    },
    {
      id: 2,
      author: 'Sarah Akinyi',
      rating: 5,
      comment: 'Inuka Properties made the entire process smooth. The team was professional and the plot in Mida Park View is perfect for my investment.',
      date: '2024-01-10',
      source: 'Google',
      verified: true,
      helpful: 8
    },
    {
      id: 3,
      author: 'David Ochieng',
      rating: 4,
      comment: 'Great experience buying land here. The location is strategic and the payment plan was flexible. Highly recommended!',
      date: '2024-01-05',
      source: 'Google',
      verified: true,
      helpful: 15
    },
    {
      id: 4,
      author: 'Grace Wanjiku',
      rating: 5,
      comment: 'I bought a plot in Ocean View Gardens and I\'m very satisfied. The beach proximity and infrastructure are excellent.',
      date: '2024-01-01',
      source: 'Google',
      verified: true,
      helpful: 6
    }
  ];

  useEffect(() => {
    const initializeReviews = async () => {
      // Test Google Places API first
      const apiWorking = await testGooglePlacesAPI();
      console.log('Google Places API Status:', apiWorking ? 'Working' : 'Not Working');
      
      // For now, use mock data (replace with real API calls when ready)
      setTimeout(() => {
        setReviews(mockGoogleReviews);
        setLoading(false);
      }, 1000);
    };

    initializeReviews();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [propertyId]);

  const handleReviewSubmit = (e) => {
    e.preventDefault();
    
    const review = {
      id: Date.now(),
      author: newReview.name,
      rating: newReview.rating,
      comment: newReview.comment,
      date: new Date().toISOString().split('T')[0],
      source: 'Customer',
      verified: false,
      helpful: 0,
      propertyExperience: newReview.propertyExperience
    };

    setReviews([review, ...reviews]);
    setNewReview({ name: '', rating: 5, comment: '', propertyExperience: 'Excellent' });
    setShowReviewForm(false);
  };

  const renderStars = (rating) => {
    return [...Array(5)].map((_, index) => (
      <Star
        key={index}
        className={`w-4 h-4 ${
          index < rating ? 'text-yellow-400 fill-current' : 'text-gray-300'
        }`}
      />
    ));
  };

  const averageRating = reviews.length > 0 
    ? (reviews.reduce((sum, review) => sum + review.rating, 0) / reviews.length).toFixed(1)
    : 0;

  if (loading) {
    return (
      <div className="bg-white rounded-lg shadow-md p-6 animate-pulse">
        <div className="h-6 bg-gray-200 rounded w-1/3 mb-4"></div>
        <div className="space-y-3">
          <div className="h-4 bg-gray-200 rounded"></div>
          <div className="h-4 bg-gray-200 rounded w-5/6"></div>
          <div className="h-4 bg-gray-200 rounded w-4/6"></div>
        </div>
      </div>
    );
  }

  return (
    <div className="bg-white rounded-lg shadow-md p-6">
      {/* Reviews Header */}
      <div className="flex items-center justify-between mb-6">
        <div>
          <h3 className="text-2xl font-bold text-gray-900 mb-2">
            Customer Reviews
          </h3>
          <div className="flex items-center space-x-4">
            <div className="flex items-center space-x-2">
              <div className="flex">
                {renderStars(Math.round(averageRating))}
              </div>
              <span className="text-lg font-semibold text-gray-900">
                {averageRating}
              </span>
            </div>
            <span className="text-gray-600"></span>
            <span className="text-gray-600">
              {reviews.length} {reviews.length === 1 ? 'review' : 'reviews'}
            </span>
            <span className="text-gray-600"></span>
            <span className="text-gray-600">{propertyLocation}</span>
          </div>
        </div>
        <button
          onClick={() => setShowReviewForm(!showReviewForm)}
          className="bg-blue-600 hover:bg-blue-700 text-white px-6 py-2 rounded-lg font-medium transition-colors flex items-center space-x-2"
        >
          <MessageCircle className="w-4 h-4" />
          <span>Write Review</span>
        </button>
      </div>

      {/* Review Form */}
      {showReviewForm && (
        <div className="bg-gray-50 rounded-lg p-6 mb-6">
          <h4 className="text-lg font-semibold text-gray-900 mb-4">
            Share Your Experience
          </h4>
          <form onSubmit={handleReviewSubmit} className="space-y-4">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Your Name
                </label>
                <input
                  type="text"
                  value={newReview.name}
                  onChange={(e) => setNewReview({...newReview, name: e.target.value})}
                  className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                  required
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Rating
                </label>
                <div className="flex space-x-1">
                  {[1, 2, 3, 4, 5].map((star) => (
                    <button
                      key={star}
                      type="button"
                      onClick={() => setNewReview({...newReview, rating: star})}
                      className="focus:outline-none"
                    >
                      <Star
                        className={`w-6 h-6 ${
                          star <= newReview.rating ? 'text-yellow-400 fill-current' : 'text-gray-300'
                        }`}
                      />
                    </button>
                  ))}
                </div>
              </div>
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Your Review
              </label>
              <textarea
                value={newReview.comment}
                onChange={(e) => setNewReview({...newReview, comment: e.target.value})}
                rows={4}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                placeholder="Share your experience with this property..."
                required
              />
            </div>
            <div className="flex justify-end space-x-3">
              <button
                type="button"
                onClick={() => setShowReviewForm(false)}
                className="px-4 py-2 text-gray-600 border border-gray-300 rounded-lg hover:bg-gray-50 transition-colors"
              >
                Cancel
              </button>
              <button
                type="submit"
                className="px-6 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
              >
                Submit Review
              </button>
            </div>
          </form>
        </div>
      )}

      {/* Reviews List */}
      <div className="space-y-6">
        {reviews.map((review) => (
          <div key={review.id} className="border-b border-gray-200 pb-6 last:border-b-0">
            <div className="flex items-start justify-between mb-3">
              <div className="flex items-center space-x-3">
                <div className="w-10 h-10 bg-blue-100 rounded-full flex items-center justify-center">
                  <User className="w-5 h-5 text-blue-600" />
                </div>
                <div>
                  <div className="flex items-center space-x-2">
                    <h5 className="font-semibold text-gray-900">{review.author}</h5>
                    {review.verified && (
                      <span className="bg-green-100 text-green-800 text-xs px-2 py-1 rounded-full">
                        Verified
                      </span>
                    )}
                  </div>
                  <div className="flex items-center space-x-2 text-sm text-gray-500">
                    <Calendar className="w-3 h-3" />
                    <span>{new Date(review.date).toLocaleDateString()}</span>
                    <span></span>
                    <span className="capitalize">{review.source}</span>
                  </div>
                </div>
              </div>
              <div className="flex items-center space-x-1">
                {renderStars(review.rating)}
              </div>
            </div>
            
            <p className="text-gray-700 mb-3 leading-relaxed">
              {review.comment}
            </p>
            
            {review.propertyExperience && (
              <div className="mb-3">
                <span className="inline-block bg-blue-50 text-blue-700 text-sm px-3 py-1 rounded-full">
                  Experience: {review.propertyExperience}
                </span>
              </div>
            )}
            
            <div className="flex items-center justify-between">
              <div className="flex items-center space-x-4 text-sm text-gray-500">
                <button className="flex items-center space-x-1 hover:text-blue-600 transition-colors">
                  <ThumbsUp className="w-4 h-4" />
                  <span>Helpful ({review.helpful})</span>
                </button>
                <button className="hover:text-blue-600 transition-colors">
                  Reply
                </button>
              </div>
            </div>
          </div>
        ))}
      </div>

      {/* No Reviews State */}
      {reviews.length === 0 && (
        <div className="text-center py-12">
          <MessageCircle className="w-16 h-16 text-gray-300 mx-auto mb-4" />
          <h4 className="text-lg font-medium text-gray-900 mb-2">No reviews yet</h4>
          <p className="text-gray-600 mb-4">
            Be the first to share your experience with this property!
          </p>
          <button
            onClick={() => setShowReviewForm(true)}
            className="bg-blue-600 hover:bg-blue-700 text-white px-6 py-2 rounded-lg font-medium transition-colors"
          >
            Write First Review
          </button>
        </div>
      )}
    </div>
  );
};

export default ReviewsSection;
