import React from 'react';
import { Link } from 'react-router-dom';
import { Phone, Mail, MapPin, Facebook, Twitter, Instagram, Linkedin, Youtube } from 'lucide-react';

const Footer = () => {
  const currentYear = new Date().getFullYear();

  const footerLinks = {
    company: [
      { name: 'Who We Are', href: '/about-us/who-we-are' },
      { name: 'Why Us', href: '/about-us/why-us' },
      { name: 'Our Team', href: '/about-us/our-team' },
      { name: 'CSR', href: '/about-us/csr' },
      { name: 'Our Partners', href: '/about-us/our-partners' },
    ],
    services: [
      { name: 'Land for Sale', href: '/land-for-sale' },
      { name: 'Property Management', href: '/about-us' },
      { name: 'Title Services', href: '/about-us' },
      { name: 'Mortgages', href: '/about-us' },
      { name: 'New Developments', href: '/land-for-sale' },
    ],
    resources: [
      { name: 'Blogs', href: '/iapl-insider/blogs' },
      { name: 'News', href: '/iapl-insider/news' },
      { name: 'Market Research', href: '/iapl-insider/market-research' },
      { name: 'Testimonials', href: '/testimonials' },
      { name: 'FAQs', href: '/contact-us' },
    ],
    locations: [
      'Mariakani',
      'Mtwapa',
      'Kikambala',
      'Kilifi',
      'Bofa',
      'Tezo',
      'Chumani',
      'Msabaha',
      'Malindi'
    ]
  };

  const socialLinks = [
    { icon: Facebook, href: '#', label: 'Facebook' },
    { icon: Twitter, href: '#', label: 'Twitter' },
    { icon: Instagram, href: '#', label: 'Instagram' },
    { icon: Linkedin, href: '#', label: 'LinkedIn' },
    { icon: Youtube, href: '#', label: 'YouTube' },
  ];

  return (
    <footer className="bg-gray-900 text-white">
      {/* Main Footer Content */}
      <div className="container-custom py-16">
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-5 gap-8">
          {/* Company Info */}
          <div className="lg:col-span-2">
            <div className="flex items-center space-x-3 mb-6">
              <div className="w-16 h-16 rounded-lg overflow-hidden">
                <img 
                  src="https://res.cloudinary.com/dyfnobo9r/image/upload/v1756029907/download_2_pwhdnm.png"
                  alt="Inuka Properties Logo"
                  className="w-full h-full object-cover"
                />
              </div>
            </div>
            
            <p className="text-gray-300 mb-6 leading-relaxed">
              Inuka Afrika Properties Limited (IAPL) is a leading real estate company 
              specializing in affordable land and housing solutions across coastal Kenya 
              and Nairobi. We're committed to helping families achieve their dream of 
              property ownership.
            </p>

            <div className="space-y-3">
              <div className="flex items-center space-x-3">
                <Phone size={16} className="text-primary-400" />
                <span className="text-gray-300">0711 02084</span>
              </div>
              <div className="flex items-center space-x-3">
                <Mail size={16} className="text-primary-400" />
                <span className="text-gray-300">info@inukaproperties.co.ke</span>
              </div>
              <div className="flex items-center space-x-3">
                <MapPin size={16} className="text-primary-400" />
                <span className="text-gray-300">Nyali, Mombasa, Kenya</span>
              </div>
            </div>
          </div>

          {/* Company Links */}
          <div>
            <h4 className="text-lg font-semibold mb-6 text-white">Company</h4>
            <ul className="space-y-3">
              {footerLinks.company.map((link) => (
                <li key={link.name}>
                  <Link 
                    to={link.href}
                    className="text-gray-300 hover:text-primary-400 transition-colors"
                  >
                    {link.name}
                  </Link>
                </li>
              ))}
            </ul>
          </div>

          {/* Services Links */}
          <div>
            <h4 className="text-lg font-semibold mb-6 text-white">Services</h4>
            <ul className="space-y-3">
              {footerLinks.services.map((link) => (
                <li key={link.name}>
                  <Link 
                    to={link.href}
                    className="text-gray-300 hover:text-primary-400 transition-colors"
                  >
                    {link.name}
                  </Link>
                </li>
              ))}
            </ul>
          </div>

          {/* Resources & Locations */}
          <div>
            <h4 className="text-lg font-semibold mb-6 text-white">Resources</h4>
            <ul className="space-y-3 mb-8">
              {footerLinks.resources.map((link) => (
                <li key={link.name}>
                  <Link 
                    to={link.href}
                    className="text-gray-300 hover:text-primary-400 transition-colors"
                  >
                    {link.name}
                  </Link>
                </li>
              ))}
            </ul>

            <h4 className="text-lg font-semibold mb-4 text-white">Our Locations</h4>
            <div className="grid grid-cols-2 gap-2">
              {footerLinks.locations.map((location) => (
                <span key={location} className="text-sm text-gray-400">
                  {location}
                </span>
              ))}
            </div>
          </div>
        </div>
      </div>

      {/* Newsletter Section */}
      <div className="border-t border-gray-800 py-8">
        <div className="container-custom">
          <div className="text-center">
            <h4 className="text-xl font-semibold mb-4">Stay Updated</h4>
            <p className="text-gray-400 mb-6">
              Subscribe to our newsletter for the latest property updates and market insights.
            </p>
            
            <div className="max-w-md mx-auto flex gap-3">
              <input
                type="email"
                placeholder="Enter your email"
                className="flex-1 px-4 py-3 bg-gray-800 border border-gray-700 rounded-lg text-white placeholder-gray-400 focus:ring-2 focus:ring-primary-500 focus:border-transparent"
              />
              <button className="btn-primary px-6">
                Subscribe
              </button>
            </div>
          </div>
        </div>
      </div>

      {/* Bottom Footer */}
      <div className="border-t border-gray-800 py-6">
        <div className="container-custom">
          <div className="flex flex-col md:flex-row items-center justify-between gap-4">
            <div className="flex flex-col md:flex-row items-center gap-4">
              <div className="text-gray-400 text-sm">
                 {currentYear} Inuka Afrika Properties Limited. All rights reserved.
              </div>
              <div className="flex items-center space-x-4">
                <Link 
                  to="/privacy-policy"
                  className="text-gray-400 hover:text-primary-400 transition-colors text-sm"
                >
                  Privacy Policy
                </Link>
                <Link 
                  to="/cookie-policy"
                  className="text-gray-400 hover:text-primary-400 transition-colors text-sm"
                >
                  Cookie Policy
                </Link>
              </div>
            </div>
            
            <div className="flex items-center space-x-6">
              {socialLinks.map((social) => (
                <a
                  key={social.label}
                  href={social.href}
                  className="text-gray-400 hover:text-primary-400 transition-colors"
                  aria-label={social.label}
                >
                  <social.icon size={20} />
                </a>
              ))}
            </div>
          </div>
        </div>
      </div>
    </footer>
  );
};

export default Footer;
