import React, { useState, useEffect, useRef, useMemo } from 'react';
import { Link } from 'react-router-dom';
import { ArrowRight, MapPin, Shield, Users, Award, Landmark, Building, ChevronLeft, ChevronRight } from 'lucide-react';
import PropertyCard from '../components/PropertyCard';
import TestimonialCard from '../components/TestimonialCard';

const HomePage = () => {
  const [currentSlide, setCurrentSlide] = useState(0);
  const [imagesLoaded, setImagesLoaded] = useState(false);
  const statsRef = useRef(null);

  // Animated counter component
  const AnimatedCounter = ({ end, duration = 2000 }) => {
    const [count, setCount] = useState(0);
    const [isAnimating, setIsAnimating] = useState(false);
    
    useEffect(() => {
      const observer = new IntersectionObserver(
        ([entry]) => {
          if (entry.isIntersecting && !isAnimating) {
            setIsAnimating(true);
          }
        },
        { threshold: 0.5 }
      );

      if (statsRef.current) {
        observer.observe(statsRef.current);
      }

      return () => observer.disconnect();
    }, [isAnimating]);

    useEffect(() => {
      if (isAnimating) {
        let startTime = null;
        const animate = (currentTime) => {
          if (!startTime) startTime = currentTime;
          const progress = Math.min((currentTime - startTime) / duration, 1);
          const currentCount = Math.floor(progress * end);
          setCount(currentCount);
          
          if (progress < 1) {
            requestAnimationFrame(animate);
          }
        };
        requestAnimationFrame(animate);
      }
    }, [end, duration, isAnimating]);
    
    // Handle decimal numbers (like 4.9 rating)
    if (end % 1 !== 0) {
      return count.toFixed(1);
    }
    return count.toLocaleString();
  };

  const heroSlides = useMemo(() => [
    {
      title: "Your Gateway to Prime",
      titleHighlight: "Land Investment",
      subtitle: "Discover exceptional land investment opportunities in coastal Kenya and Nairobi outskirts with secure title deeds and flexible payment plans.",
      subtitle2: "Join hundreds of satisfied investors who have already secured their future with Inuka Properties.",
      ctaText: "Explore Land Plots",
      ctaLink: "/land-for-sale",
      backgroundImage: "https://res.cloudinary.com/dyfnobo9r/image/upload/v1756037951/chumani-phase-3-4_jjefim.jpg"
    },
    {
      title: "Strategic Land Locations",
      titleHighlight: "Across Kenya",
      subtitle: "Prime land plots in high-growth areas offering excellent investment potential and secure title deeds for future development.",
      subtitle2: "From Coastal Kenya to Nairobi Outskirts - Your Investment Journey Starts Here.",
      ctaText: "View Land Locations",
      ctaLink: "/about-us",
      backgroundImage: "https://res.cloudinary.com/dyfnobo9r/image/upload/v1756039213/Mida-park-view-3_jghoyg.jpg"
    },
    {
      title: "Trusted by 1000+",
      titleHighlight: "Land Investors",
      subtitle: "15+ Years of Land Investment Excellence with a proven track record of successful land transactions.",
      subtitle2: "Experience the difference with Inuka Properties - Where Dreams Become Reality.",
      ctaText: "Book a Site Visit",
      ctaLink: "/contact-us",
      backgroundImage: "https://res.cloudinary.com/dyfnobo9r/image/upload/v1756039651/Ocean_View_Gardens_sey25b.jpg"
    }
  ], []);

  // Preload images for better quality
  useEffect(() => {
    const preloadImages = async () => {
      const imagePromises = heroSlides.map((slide) => {
        return new Promise((resolve, reject) => {
          const img = new Image();
          img.onload = resolve;
          img.onerror = reject;
          img.src = slide.backgroundImage;
        });
      });
      
      try {
        await Promise.all(imagePromises);
        setImagesLoaded(true);
      } catch (error) {
        console.log('Some images failed to load:', error);
        setImagesLoaded(true); // Continue anyway
      }
    };
    
    preloadImages();
  }, [heroSlides]);

  useEffect(() => {
    const timer = setInterval(() => {
      setCurrentSlide((prev) => (prev + 1) % heroSlides.length);
    }, 5000);
    return () => clearInterval(timer);
  }, [heroSlides.length]);

  const nextSlide = () => {
    setCurrentSlide((prev) => (prev + 1) % heroSlides.length);
  };

  const prevSlide = () => {
    setCurrentSlide((prev) => (prev - 1 + heroSlides.length) % heroSlides.length);
  };

  const goToSlide = (index) => {
    setCurrentSlide(index);
  };



  const featuredProperties = [
    {
      id: 13,
      name: "Chumani Beach Plots",
      location: "Chumani",
      price: "KES 650,000",
      size: "1/8 Acre",
      status: "Available",
      description: "Dreaming of a life by the sea? Make it a reality at Chumani Beach Plots!  Just 600 meters from the sand, these plots offer incredible panoramic views. Build your perfect getaway home or invest in a piece of paradise. The ocean is calling!",
      features: ["Beach Proximity", "Ocean Views", "Panoramic Views", "Perfect Getaway", "Investment Opportunity", "Coastal Living", "600m to Beach", "Paradise Location"],
      image: "https://res.cloudinary.com/dyfnobo9r/image/upload/v1756190400/Chumani_beach_view_2_qcww2d.jpg",
      virtualTour: "https://youtu.be/NCWIKuiWPHw?si=KBf3ndqIv6NMuNOB",
      deposit: "KES 250,000",
      paymentPlan: "12 monthly installments",
      plotSize: "1/8 Acre"
    },
    {
      id: 1,
      name: "Chumani Phase 3",
      location: "Chumani, Kilifi",
      price: "KES 550,000",
      size: "1/8 Acre",
      status: "Available",
      description: "Prime 1/8 Acre Plot for SaleIdeal for Residential Development | Water & Electricity Available. Looking to build your dream home? These ready-to-build residential plots are perfect for immediate settlement and development.",
      features: ["Water Available", "Electricity Available", "Access Road", "Security", "Title Deed", "Ready to Build"],
      image: "https://res.cloudinary.com/dyfnobo9r/image/upload/v1756037951/chumani-phase-3-4_jjefim.jpg",
      virtualTour: "https://youtu.be/NCWIKuiWPHw?si=KBf3ndqIv6NMuNOB",
      deposit: "KES 150,000",
      paymentPlan: "12 monthly installments",
      plotSize: "1/8 Acre"
    },
    {
      id: 2,
      name: "Mida Park View Phase 1",
      location: "Mida, Kilifi",
      price: "KES 350,000",
      size: "1/8 Acre",
      status: "Available",
      description: "Located in Mida, just before Mida Msikitini stage on the right-hand side towards Malindi. The project is approximately 800 meters from the Kilifi-Malindi highway.",
      features: ["Park View", "Access Road", "Security", "Perimeter Fence", "Highway Proximity", "Murram Road Access"],
      image: "https://res.cloudinary.com/dyfnobo9r/image/upload/v1756039213/Mida-park-view-3_jghoyg.jpg",
      virtualTour: "https://youtu.be/NCWIKuiWPHw?si=KBf3ndqIv6NMuNOB"
    }
  ];

  const locations = [
    "Mariakani", "Mtwapa", "Kikambala", "Kilifi", "Bofa",
    "Tezo", "Chumani", "Msabaha", "Malindi"
  ];

  const stats = [
    { icon: Award, number: 9, label: "Years of experience" },
    { icon: Building, number: 147, label: "Projects Completed" },
    { icon: Shield, number: 4513, label: "Titled deeds Disbursed" },
    { icon: Users, number: 200, label: "Work Force" }
  ];





  const services = [
    {
      icon: Landmark,
      title: "Land Plot Sales",
      description: "Premium land plots in strategic locations with secure title deeds ready for development",
      link: "/land-for-sale"
    },
    {
      icon: Building,
      title: "Land Development",
      description: "Professional land development services and infrastructure planning for your plots",
      link: "/about-us"
    },
    {
      icon: Shield,
      title: "Title Services",
      description: "Complete legal support and title deed processing for land ownership",
      link: "/about-us"
    }
  ];

  const testimonials = [
    {
      id: 1,
      name: "John Kamau",
      role: "Land Investor",
      content: "Inuka Properties helped me secure a great land plot in Kilifi. The process was smooth and professional.",
      rating: 5,
      image: "https://images.unsplash.com/photo-1507003211169-0a1dd7228f2d?ixlib=rb-4.0.3&auto=format&fit=crop&w=200&q=80"
    },
    {
      id: 2,
      name: "Sarah Wanjiku",
      role: "Land Plot Owner",
      content: "Excellent service from start to finish. I now have my dream land plot with all legal documents sorted.",
      rating: 5,
      image: "https://images.unsplash.com/photo-1494790108755-2616b612b786?ixlib=rb-4.0.3&auto=format&fit=crop&w=200&q=80"
    },
    {
      id: 3,
      name: "David Mwangi",
      role: "Land Developer",
      content: "The team at Inuka Properties is knowledgeable and trustworthy. Highly recommended for land investment!",
      rating: 5,
      image: "https://images.unsplash.com/photo-1472099645785-5658abf4ff4e?ixlib=rb-4.0.3&auto=format&fit=crop&w=200&q=80"
    }
  ];

  return (
    <div className="min-h-screen">
                     {/* Hero Carousel Section */}
        <section className="relative h-screen overflow-hidden">
          {/* Loading Overlay */}
          {!imagesLoaded && (
            <div className="absolute inset-0 bg-gray-900 flex items-center justify-center z-10">
              <div className="text-white text-center">
                <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-white mx-auto mb-4"></div>
                <p className="text-lg">Loading...</p>
              </div>
            </div>
          )}
                   {/* Carousel Slides */}
          {heroSlides.map((slide, index) => (
            <div
              key={index}
              className={`absolute inset-0 transition-opacity duration-1000 ${
                index === currentSlide ? 'opacity-100' : 'opacity-0'
              } ${imagesLoaded ? 'opacity-100' : 'opacity-50'}`}
            >
                           {/* Background Image */}
              <div 
                className="absolute inset-0 bg-cover bg-center bg-no-repeat"
                style={{ 
                  backgroundImage: `url(${slide.backgroundImage})`,
                  filter: 'brightness(1.2) contrast(1.3) saturate(1.1)',
                  imageRendering: 'crisp-edges',
                  backfaceVisibility: 'hidden'
                }}
              >
                <div className="absolute inset-0 bg-black/20"></div>
                <div className="absolute inset-0 bg-gradient-to-r from-black/30 via-black/15 to-black/5"></div>
              </div>

             {/* Content */}
             <div className="relative h-full flex items-center justify-center">
               <div className="container-custom section-padding">
                 <div className="text-center max-w-5xl mx-auto">
                   <div className="space-y-8 text-white">
                     <div className="space-y-6">
                                               <h1 className="text-6xl lg:text-7xl font-bold leading-tight">
                          <span className="text-white">{slide.title}</span>
                          <span className="text-secondary-500 ml-2">{slide.titleHighlight}</span>
                        </h1>
                       <div className="space-y-4">
                         <p className="text-xl text-gray-200 leading-relaxed max-w-4xl mx-auto">
                           {slide.subtitle}
                         </p>
                         <p className="text-xl text-gray-200 leading-relaxed max-w-4xl mx-auto">
                           {slide.subtitle2}
                         </p>
                       </div>
                     </div>
                     
                                           <div className="flex justify-center">
                        <Link to={slide.ctaLink} className="inline-flex items-center bg-secondary-500 hover:bg-secondary-600 text-white font-bold py-4 px-8 rounded-lg transition-all duration-300 transform hover:scale-105">
                          {slide.ctaText}
                          <ArrowRight size={20} className="ml-2" />
                        </Link>
                      </div>
                   </div>
                 </div>
               </div>
             </div>
           </div>
         ))}

         {/* Carousel Navigation */}
         <div className="absolute bottom-8 left-1/2 transform -translate-x-1/2 flex items-center space-x-4">
           <button
             onClick={prevSlide}
             className="w-12 h-12 bg-white/20 hover:bg-white/30 backdrop-blur-sm rounded-full flex items-center justify-center text-white transition-all duration-300"
           >
             <ChevronLeft size={24} />
           </button>
           
           {/* Dots */}
           <div className="flex space-x-2">
             {heroSlides.map((_, index) => (
               <button
                 key={index}
                 onClick={() => goToSlide(index)}
                 className={`w-3 h-3 rounded-full transition-all duration-300 ${
                   index === currentSlide ? 'bg-white scale-125' : 'bg-white/50 hover:bg-white/75'
                 }`}
               />
             ))}
           </div>
           
           <button
             onClick={nextSlide}
             className="w-12 h-12 bg-white/20 hover:bg-white/30 backdrop-blur-sm rounded-full flex items-center justify-center text-white transition-all duration-300"
           >
             <ChevronRight size={24} />
           </button>
         </div>

         
       </section>

             {/* Stats Section */}
       <section ref={statsRef} className="bg-white py-16">
         <div className="container-custom">
           <div className="grid grid-cols-2 lg:grid-cols-4 gap-8">
             {stats.map((stat, index) => (
               <div key={index} className="text-center">
                 <div className="w-16 h-16 bg-primary-100 rounded-full flex items-center justify-center mx-auto mb-4 transform hover:scale-110 transition-transform duration-300">
                   <stat.icon size={32} className="text-primary-500" />
                 </div>
                 <div className="text-3xl font-bold text-gray-900 mb-2">
                   <AnimatedCounter end={stat.number} />+
                 </div>
                 <div className="text-gray-600">{stat.label}</div>
               </div>
             ))}
           </div>
         </div>
       </section>

             {/* Why Choose Us Section */}
       <section className="bg-gray-900 py-16">
         <div className="container-custom">
           <div className="grid lg:grid-cols-2 gap-16 items-start">
             {/* Left Column */}
             <div className="space-y-6">
               <h2 className="text-4xl font-bold text-white mb-6">Your trusted real estate partner</h2>
               <p className="text-xl text-gray-300 leading-relaxed">
                 Our real estate company has a number of prime properties in prime locations.
               </p>
               <Link to="/services" className="inline-flex items-center text-primary-400 hover:text-primary-300 font-semibold text-lg transition-colors duration-300">
                 Services 
               </Link>
             </div>

             {/* Right Column */}
             <div className="space-y-8">
               <h2 className="text-4xl font-bold text-white mb-8">Why Inuka Properties</h2>
               <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                 {/* Return On Investment */}
                 <div className="space-y-3">
                   <div className="flex items-center space-x-3">
                     <div className="w-8 h-8 bg-primary-500 rounded-full flex items-center justify-center">
                       <svg className="w-4 h-4 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                         <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 7h8m0 0v8m0-8l-8 8-4-4-6 6" />
                       </svg>
                     </div>
                     <h3 className="text-lg font-bold text-white">Return On Investment</h3>
                   </div>
                   <p className="text-gray-300 text-sm leading-relaxed">
                     All our projects are located at strategic locations giving you a guaranteed return on investment.
                   </p>
                 </div>

                 {/* Genuine */}
                 <div className="space-y-3">
                   <div className="flex items-center space-x-3">
                     <div className="w-8 h-8 bg-primary-500 rounded-full flex items-center justify-center">
                       <svg className="w-4 h-4 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                         <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
                       </svg>
                     </div>
                     <h3 className="text-lg font-bold text-white">Genuine</h3>
                   </div>
                   <p className="text-gray-300 text-sm leading-relaxed">
                     Due diligence is a priority to offer you genuine land
                   </p>
                 </div>

                 {/* Affordable */}
                 <div className="space-y-3">
                   <div className="flex items-center space-x-3">
                     <div className="w-8 h-8 bg-primary-500 rounded-full flex items-center justify-center">
                       <svg className="w-4 h-4 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                         <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8c-1.657 0-3 .895-3 2s1.343 2 3 2 3 .895 3 2-1.343 2-3 2m0-8c1.11 0 2.08.402 2.599 1M12 8V7m0 1v8m0 0v1m0-1c-1.11 0-2.08-.402-2.599-1" />
                       </svg>
                     </div>
                     <h3 className="text-lg font-bold text-white">Affordable</h3>
                   </div>
                   <p className="text-gray-300 text-sm leading-relaxed">
                     Our products are affordable from as low as 199,000.
                   </p>
                 </div>

                 {/* Flexibility */}
                 <div className="space-y-3">
                   <div className="flex items-center space-x-3">
                     <div className="w-8 h-8 bg-primary-500 rounded-full flex items-center justify-center">
                       <svg className="w-4 h-4 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                         <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" />
                       </svg>
                     </div>
                     <h3 className="text-lg font-bold text-white">Flexibility</h3>
                   </div>
                   <p className="text-gray-300 text-sm leading-relaxed">
                     We offer flexible terms of payments at no interest.
                   </p>
                 </div>

                 {/* Title Deeds */}
                 <div className="space-y-3">
                   <div className="flex items-center space-x-3">
                     <div className="w-8 h-8 bg-primary-500 rounded-full flex items-center justify-center">
                       <svg className="w-4 h-4 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                         <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
                       </svg>
                     </div>
                     <h3 className="text-lg font-bold text-white">Title Deeds</h3>
                   </div>
                   <p className="text-gray-300 text-sm leading-relaxed">
                     All our properties come with title deeds. Our title deed processing is fast and efficient.
                   </p>
                 </div>

                 {/* Value Addition */}
                 <div className="space-y-3">
                   <div className="flex items-center space-x-3">
                     <div className="w-8 h-8 bg-primary-500 rounded-full flex items-center justify-center">
                       <svg className="w-4 h-4 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                         <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 6v6m0 0v6m0-6h6m-6 0H6" />
                       </svg>
                     </div>
                     <h3 className="text-lg font-bold text-white">Value Addition</h3>
                   </div>
                   <p className="text-gray-300 text-sm leading-relaxed">
                     We offer value addition to our projects e.g. fencing, access roads and bush clearing.
                   </p>
                 </div>
               </div>
             </div>
           </div>
         </div>
       </section>

      {/* Services Section */}
      <section className="py-16">
        <div className="container-custom">
          <div className="text-center mb-16">
            <h2 className="text-4xl font-bold text-gray-900 mb-4">Our Land Investment Services</h2>
            <p className="text-xl text-gray-600 max-w-3xl mx-auto">
              We offer comprehensive land investment solutions to meet all your plot acquisition needs, 
              from land selection to title processing.
            </p>
          </div>

          <div className="grid md:grid-cols-3 gap-8">
            {services.map((service, index) => (
              <div key={index} className="card p-8 text-center">
                <div className="w-16 h-16 bg-primary-100 rounded-full flex items-center justify-center mx-auto mb-6">
                  <service.icon size={32} className="text-primary-500" />
                </div>
                <h3 className="text-2xl font-bold text-gray-900 mb-4">{service.title}</h3>
                <p className="text-gray-600 mb-6">{service.description}</p>
                <Link to={service.link} className="text-primary-500 hover:text-primary-600 font-semibold">
                  Learn More 
                </Link>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* Locations Section */}
      <section className="bg-primary-50 py-16">
        <div className="container-custom">
          <div className="text-center mb-16">
            <h2 className="text-4xl font-bold text-gray-900 mb-4">Our Land Plot Locations</h2>
            <p className="text-xl text-gray-600 max-w-3xl mx-auto">
              We have land plots across strategic locations in coastal Kenya and Nairobi outskirts, 
              offering diverse investment opportunities for future development.
            </p>
          </div>

          <div className="grid grid-cols-3 md:grid-cols-5 lg:grid-cols-9 gap-4">
            {locations.map((location, index) => (
              <div key={index} className="text-center">
                <div className="bg-white rounded-lg p-4 shadow-md hover:shadow-lg transition-shadow">
                  <MapPin size={24} className="text-primary-500 mx-auto mb-2" />
                  <span className="text-sm font-medium text-gray-700">{location}</span>
                </div>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* Featured Properties Section */}
      <section className="py-16">
        <div className="container-custom">
          <div className="text-center mb-16">
            <h2 className="text-4xl font-bold text-gray-900 mb-4">Featured Land Plots</h2>
            <p className="text-xl text-gray-600 max-w-3xl mx-auto">
              Discover our most popular land developments with excellent investment potential 
              and strategic locations ready for development.
            </p>
          </div>

          <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-8">
            {featuredProperties.map((property) => (
              <PropertyCard key={property.id} property={property} />
            ))}
          </div>

          <div className="text-center mt-12">
            <Link to="/land-for-sale" className="btn-primary text-lg px-8 py-4">
              View All Land Plots
              <ArrowRight size={20} className="ml-2" />
            </Link>
          </div>
        </div>
      </section>

      {/* Testimonials Section */}
      <section className="bg-primary-50 py-16">
        <div className="container-custom">
          <div className="text-center mb-16">
            <h2 className="text-4xl font-bold text-gray-900 mb-4">What Our Land Investors Say</h2>
            <p className="text-xl text-gray-600 max-w-3xl mx-auto">
              Don't just take our word for it. Hear from our satisfied land investors about their 
              experience with Inuka Properties.
            </p>
          </div>

          <div className="grid md:grid-cols-3 gap-8">
            {testimonials.map((testimonial) => (
              <TestimonialCard key={testimonial.id} testimonial={testimonial} />
            ))}
          </div>
        </div>
      </section>

             {/* CTA Section */}
       <section className="bg-gradient-to-r from-primary-500 to-primary-600 text-white py-16">
         <div className="container-custom text-center">
           <h2 className="text-4xl font-bold mb-6">Ready to Start Your Land Investment Journey?</h2>
           <p className="text-xl text-gray-200 mb-8 max-w-2xl mx-auto">
             Join hundreds of satisfied land investors who have already secured their future with 
             Inuka Properties land plots. Contact us today for a personalized consultation.
           </p>
           <div className="flex flex-col sm:flex-row gap-4 justify-center">
             <Link to="/contact-us" className="btn-secondary text-lg px-8 py-4">
               Book a Site Visit
             </Link>
             <Link to="/land-for-sale" className="btn-outline border-white text-white hover:bg-white hover:text-primary-500 text-lg px-8 py-4">
               Browse Land Plots
             </Link>
           </div>
                 </div>
      </section>
    </div>
  );
};

export default HomePage;
